import {Environment} from "./environment/environment";
import {Interface} from "./display/interface";
import { HighlightUtils } from './utility/HighlightUtils.js';
import { RenderUtils } from './utility/RenderUtils.js';
import { envState, loadEnvSettings } from './settings';
import * as d3 from "d3";
import {OzAssistant} from "./assistant/OzAssistant";


// load default initial app state/settings
await loadEnvSettings('./data/settings/default.json');

// create environment
const environment = new Environment();

// init a quasi random environment (fixed clipping polygon)
environment.initRandom();

// generate a random environment (incl. random clipping polygon)
