// create a (global singleton) container for highlighting
// IMPORTANT: always do this before the D3 drawing objects are instantiated!!
export const highlightUtils = new HighlightUtils();

// create a (global singleton) container for triggering render() across (all) display objects
export const renderUtils = new RenderUtils();

// create interfaces (that also setup the highlighting and rendering referencing)
const display = new Interface(environment);

