// Create a scripted AI system for prototyping AI support in sectorization,
// let's call it OzAssistent as in "wizard of Oz" ;)

const assistant = new OzAssistant(() => envState.assistantTab === 'ai-container');
await assistant.load("./data/oz/defaultOz.json");
assistant.start();

let time = envState.initTime;

// init the timer
function updateTimer() {

    //time = envState.initTime;

    const hrs = Math.floor((time) / 3600);
    const mins = Math.floor((time) % 3600 / 60);
    const secs = (time) % 60;

    document.getElementById("timer").textContent =
        String(hrs).padStart(2, '0') + ":" +
        String(mins).padStart(2, '0') + ":" +
        String(secs).padStart(2, '0');

    time++;
}

// update the timer every 1 sec (1000 ms)
setInterval(updateTimer, 1000);

