import * as d3 from 'd3';
import polygonClipping from 'polygon-clipping';
import {
    calculateIntersections,
    calculateLineCrossings, doLinesIntersect, getLineSegmentLength, getProblematicLineCrossings, getProblematicVertices,
    getTrackLocationAtDistance,
    getUnitDirectionVector,
    isPointInsidePolygon, pointToLineDistance
} from "../utility/geometry";
import { envState } from "../settings";
import {Perlin, Simplex} from "../utility/noise";
import {renderUtils} from "../newmain";

// pathfinders
const { Astar } = require('../pathfinder/astar');
const { Thetastar } = require('../pathfinder/thetastar');

export class Environment {
    constructor() {
        this.voronoiPoints = [];
        this.width = 800;
        this.height = 600;
        this.targetPoints = [];
        this.polygons = [];
        this.voronoiVertices = [];
        this.lines = [];
        this.dynamicLines = [];
        this.crossings = [];
        this.particles = [];
        this.particlePositions = [];
        this.numberOfParticles = 10;
        this.numLinePolygonIntersections = [];
        this.selectedPolygon = [];
        this.selectedPolygonIdx = -1;
        this.timeValue = 0;
        this.timeOffset = 0;
        this.voronoiSpeeds = [];
        this.selectedPoint = null;
        this.editPolygonIndices = [];
        this.editPolygons = [];
        this.editVertices = [];
        this.clippingPolygon = [];
        this.cellSize = 0;
        this.gridCells = [];
        this.selectedCells = new Set();
        this.heatmapGrid = [];

        this.clusterStability = NaN;
        this.startTime = null;

        this.problemLinePolygonCrossings = [];
        this.intersectionPoints = [];
        this.problemSegments = [];
        this.problemVertices = [];

        this.wind_type = 'natural';
        this.wind_vectors = [];
        this.wind_loaded = false;

        //this.initRandom();
    }

    // init a random environment
    initRandom() {

        // init dimension of environment
        this.setDimension(800,600);

        // define a clipping polygon
        this.clippingPolygon = [[
            [25, 25], [425, 25], [775, 25], [775, 300],
            [775, 575], [425, 575], [25, 575], [25, 300]
        ]];

        // setup the grid (for path-planning and heatmap)
        this.initGrid(20);

        // generate 10 random lines
        this.generateRandomLines(10);

        // generate 10 random Voronoi centers
        this.generateRandomPoints(10);
        this.generateRandomSpeeds(this.voronoiPoints.length);

        // generate random Particles along random lines
        this.generateRandomParticles(this.lines, 10);

        // init pathfinder
        this.pathFinding();

        // update environment
        this.update();
    }

    // generate a random environment
    generateRandom(width, height, numVoronoiPoints, numLines, numLineParticles) {

        // init dimension of environment
        this.setDimension(width,height);

        // define a clipping polygon
        this.clippingPolygon = this.generateRandomConvexPolygon(8,width, height);

        // setup the grid (for path-planning and heatmap)
        this.initGrid(20);

        // generate 10 random lines
        this.generateRandomLines(numLines);

        // generate 10 random Voronoi centers
        this.generateRandomPoints(numVoronoiPoints);
        this.generateRandomSpeeds(this.voronoiPoints.length);

        // generate random Particles along random lines
        this.generateRandomParticles(this.lines, numLineParticles);

        // init pathfinder
        this.pathFinding();

        // update environment
        this.update();
    }

    setDimension(width, height) {
        this.width = width;
        this.height = height;
    }

    initGrid(cellSize) {
        this.cellSize = cellSize;
        this.gridCells = [];
        let cellIndex = 0;
        for (let x = 0; x < this.width; x += cellSize) {
            for (let y = 0; y < this.height; y += cellSize) {
                this.gridCells.push({ x, y, index: cellIndex++ });
            }
        }
    }

    generateRandomConvexPolygon(numPoints, width, height) {
        const xs = [], ys = [];
        for (let i = 0; i < numPoints; i++) {
            xs.push(Math.random() * width);
            ys.push(Math.random() * height);
        }

        xs.sort((a, b) => a - b);
        ys.sort((a, b) => a - b);

        const xVec = [], yVec = [];
        let minX = xs[0], maxX = xs[xs.length - 1];
        let minY = ys[0], maxY = ys[ys.length - 1];

        let lastTop = minX, lastBottom = minX;
        for (let i = 1; i < numPoints - 1; i++) {
            let x = xs[i];
            if (Math.random() < 0.5) {
                xVec.push(x - lastTop);
                lastTop = x;
            } else {
                xVec.push(lastBottom - x);
                lastBottom = x;
            }
        }
        xVec.push(maxX - lastTop);
        xVec.push(lastBottom - maxX);

        let lastLeft = minY, lastRight = minY;
        for (let i = 1; i < numPoints - 1; i++) {
            let y = ys[i];
            if (Math.random() < 0.5) {
                yVec.push(y - lastLeft);
                lastLeft = y;
            } else {
                yVec.push(lastRight - y);
                lastRight = y;
            }
        }
        yVec.push(maxY - lastLeft);
        yVec.push(lastRight - maxY);

        const vectors = [];
        for (let i = 0; i < numPoints; i++) {
            vectors.push({ x: xVec[i], y: yVec[i] });
        }

        vectors.sort((a, b) => Math.atan2(a.y, a.x) - Math.atan2(b.y, b.x));

        let x = 0, y = 0;
        let points = [];
        for (const v of vectors) {
            x += v.x;
            y += v.y;
            points.push([x, y]);
        }

        // Get bounding box of the generated polygon
        let minPX = Math.min(...points.map(p => p[0]));
        let maxPX = Math.max(...points.map(p => p[0]));
        let minPY = Math.min(...points.map(p => p[1]));
        let maxPY = Math.max(...points.map(p => p[1]));

        let scaleX = width / (maxPX - minPX);
        let scaleY = height / (maxPY - minPY);
        let scale = Math.min(scaleX, scaleY) * 0.95; // 95% to leave slight margin

        // Scale and center the polygon
        const finalPoints = points.map(([px, py]) => [
            (px - minPX) * scale,
            (py - minPY) * scale
        ]).map(([sx, sy]) => [
            sx + (width - (maxPX - minPX) * scale) / 2,
            sy + (height - (maxPY - minPY) * scale) / 2
        ]);

        return [finalPoints];
    }

    generateRandomLines(numLines) {
        const lines = [];
        const half = Math.floor(numLines / 2);
        let alternate = false;

        for (let i = 0; i < half; i++) {
            const x1 = 0.0; //Math.random() * width;
            const y1 = Math.floor(Math.random() * this.height);
            const x2 = this.width; //Math.random() * width;
            const y2 = Math.floor(Math.random() * this.height);
            if(alternate)
                lines.push([[x2, y2], [x1, y1]]);
            else
                lines.push([[x1, y1], [x2, y2]]);
            alternate = !alternate;
        }

        alternate = false;

        for (let i = 0; i < half; i++) {
            const x1 = Math.floor(Math.random() * this.width);
            const y1 = 0.0; //Math.random() * height;
            const x2 = Math.floor(Math.random() * this.width);
            const y2 = this.height; //Math.random() * height;
            if(alternate)
                lines.push([[x2, y2], [x1, y1]]);
            else
                lines.push([[x1, y1], [x2, y2]]);
            alternate = !alternate;
        }
        this.lines = lines;
    }

    jitterLines(distance) {
        const newlines = [];
        this.lines.forEach(line => {
            //const [lineStart, lineEnd] = line;
            const lineStart = line[0];
            const lineEnd = line[line.length - 1];
            let random_sign = -1 + Math.round(Math.random()) * 2;
            let x1 = lineStart[0];
            let y1 = lineStart[1];

            if(x1 === 0) {
                x1 = 0;
                y1 += ( random_sign * Math.floor(Math.random() * distance ));
            } else if(x1 === this.width) {
                x1 = this.width;
                y1 += ( random_sign * Math.floor(Math.random() * distance ));
            } else {
                x1 += ( random_sign * Math.floor(Math.random() * distance ));
            }

            random_sign = -1 + Math.round(Math.random()) * 2;
            let x2 = lineEnd[0];
            let y2 = lineEnd[1];

            if(x2 === 0) {
                x2 = 0;
                y2 += ( random_sign * Math.floor(Math.random() * distance ));
            } else if(x2 === this.width) {
                x2 = this.width;
                y2 += ( random_sign * Math.floor(Math.random() * distance ));
            } else {
                x2 += ( random_sign * Math.floor(Math.random() * distance ));
            }

            newlines.push([[x1, y1], [x2, y2]]);
        });
        this.lines = newlines;
    }

    generateRandomPoints(num) {
        this.voronoiPoints = Array.from({ length: num }, () => [
            Math.random() * this.width,
            Math.random() * this.height
        ]);
    }

    generateRandomSpeeds(num) {
        this.voronoiSpeeds = Array.from({ length: num }, () => [
            (Math.random() * 4) - 2,
            (Math.random() * 4) - 2
        ]);
    }

    generateRandomParticles(lines, numParticles) {
        const particles = [];

        for(let i=0; i<lines.length; i++) {

            let start = lines[i][0]; // first point is origin
            let end = lines[i][lines[i].length - 1]; // last point is target

            // each line has a fixed particle speed to reduce number of in-trail conflicts
            const particle_speed = 7.5 + Math.random(); // speed approximate: 450 kts = 7.5 nm / min (slider bar is in "minutes")

            // create a list of random particle start times while ensuring a minimum time separation
            // between particles to further avoid in-trail conflicts and overlapping particles
            let startTimes = this.generateRandomStartTimes(0,120,2,numParticles);
            let randomTime = [];

            for (let j = 0; j < numParticles; j++) {

                if (startTimes.length > 0) {
                    const randomIndex = Math.floor(Math.random() * startTimes.length);
                    randomTime = startTimes[randomIndex];
                    startTimes.splice(randomIndex, 1);
                }
                else {
                    randomTime = 122;
                }

                // create the particle
                const particle = {
                    track: lines[i],
                    pos: start,
                    direction: getUnitDirectionVector(start,end),
                    speed: particle_speed,
                    time: randomTime,
                    offset: 0
                }
                particles.push(particle);
            }
        }
        this.particles = particles;
    }

    generateRandomStartTimes(min, max, minTimeSeparation, count) {
        const coordinates = [];

        let attempts = 0;
        const maxAttempts = 1000;

        while (coordinates.length < count && attempts < maxAttempts) {
            const candidate = Math.round(Math.random() * (max - min + 1)) + min;

            // Check if it's far enough from all existing coordinates
            const isValid = coordinates.every(coord => Math.abs(coord - candidate) >= minTimeSeparation);

            if (isValid) {
                coordinates.push(candidate);
            }

            attempts++;
        }

        if (coordinates.length < count) {
            console.warn(`Only generated ${coordinates.length} coordinates out of requested ${count}. Try adjusting range or minDistance.`);
        }

        return coordinates;
    }

    offsetParticles(offset) {
        this.particles.forEach(particle => {
            particle.offset = -parseInt(offset);
        });
    }

    pathFinding() {
        const mesh = {
            width: this.width,
            height: this.height,
            size: this.cellSize,
            blockedIndices: [...this.selectedCells]
        };

        let planner = undefined;

        if(envState.pathFinder === "Dijkstra")
            planner = new Astar(mesh);
        else if(envState.pathFinder === "Astar")
            planner = new Astar(mesh, 'euclidean');
        else if(envState.pathFinder === "Thetastar")
            planner = new Thetastar(mesh);

        this.lines = this.findPaths(this.lines, planner);
    }

    findPaths(lines, planner) {
        let newLines = [];
        lines.forEach((line) => {
            const path = planner.search(line[0], line[line.length - 1]);
            if(path.length)
                newLines.push(path);
        });
       return newLines;
    }

    computeVoronoi() {
        if(this.voronoiPoints.length === 0 || this.clippingPolygon.length === 0) return;

        const delaunay = d3.Delaunay.from(this.voronoiPoints);
        const voronoi = delaunay.voronoi([0, 0, this.width, this.height]);

        let poly = this.voronoiPoints.map((_, i) => voronoi.cellPolygon(i)).filter(p => p);

        // Convert Voronoi polygons into clipped polygons
        let clippedPolygons = poly.map(p => polygonClipping.intersection([p], this.clippingPolygon)).flat(2).filter(p => p.length > 0);

        // Collect unique Voronoi vertices
        this.voronoiVertices = new Set();
        clippedPolygons.forEach(poly => poly.forEach(([x, y]) => this.voronoiVertices.add(`${x},${y}`)));

        this.polygons = clippedPolygons;

        // Extract unique Voronoi vertices
        const vertexSet = new Set();
        this.polygons.forEach(polygon => {
            polygon.forEach(([x, y]) => {
                vertexSet.add(`${x},${y}`); // Store as string to ensure uniqueness
            });
        });

        this.voronoiVertices = [...vertexSet].map(v => v.split(",").map(Number)); // Convert back to [x, y]

        // prepare for when switching from Voronoi polygons to normal polygons
        this.editPolygons = [];
        this.editVertices = [];
        this.editPolygonIndices = [];

        for (let i = 0; i < this.polygons.length; i++) {
            const indices = this.polygons[i].map(pt => this.getEditVertexIndex(pt,this.editVertices));
            this.editPolygonIndices.push(indices);
        }
    }

    getEditVertexIndex(v, vertices, tolerance = 0.1) {
        for (let i = 0; i < vertices.length; i++) {
            const [x, y] = vertices[i];
            if (Math.abs(x - v[0]) < tolerance && Math.abs(y - v[1]) < tolerance) {
                return i;
            }
        }
        vertices.push(v);
        return vertices.length - 1;
    }

    updateEditPolygons() {
        this.editPolygons = this.editPolygonIndices.map(polygonIndices =>
            polygonIndices.map(index => {
                const vertex = this.editVertices[index];
                if (!vertex || vertex.length < 2) {
                    throw new Error(`Invalid vertex index: ${index}`);
                }
                return [vertex[0], vertex[1]];
            })
        );
    }

    getNearestCell(x,y) {
        const gridX = Math.floor(x / this.cellSize) * this.cellSize;
        const gridY = Math.floor(y / this.cellSize) * this.cellSize;
        return this.gridCells.find(c => c.x === gridX && c.y === gridY);
    }

    updateHeatMap() {
        this.heatmapGrid = new Array(this.gridCells.length).fill(0);

        for (let i= 0; i < 240; i++) {
            const positions = this.updateParticles(i, false);

            positions.forEach(pt => {
                let cell = [];
                cell = this.getNearestCell(pt[0],pt[1]);
                if(cell && isPointInsidePolygon(pt,this.clippingPolygon[0])) {
                    this.heatmapGrid[cell.index]++;
                }
            });
        }
    }

    updateParticles(time, includeOffset = true) {
        const part_pos = [];
        this.particles.forEach(particle => {
            if( time >= (particle.time + (includeOffset ? particle.offset : 0)) ) {
                const dt = time - (particle.time + (includeOffset ? particle.offset : 0));
                const travelDist = particle.speed * dt;
                const [x, y] = getTrackLocationAtDistance(particle.track, travelDist);
                part_pos.push([x, y]);
            }
        });
        return part_pos;
    }

    calculateParticleSeparationProblems(positions) {
        let problemPositions = [];
        for (let i = 0; i < positions.length; i++) {
            for (let j = i + 1; j < positions.length; j++) {
                const p1 = positions[i];
                const p2 = positions[j];
                if (getLineSegmentLength(p1, p2) < 5.0) {
                    problemPositions.push([p1, p2]);
                }
            }
        }
        return problemPositions;
    }

    getTimeLineData() {
        if(this.selectedPolygonIdx === -1 || !this.selectedPolygon) return;

        //console.log("time line data generation");

        // get the up-to-date polygon vertices (which can change, for example, when dragging Voronoi points)
        this.selectedPolygon = (envState.editMode === 'voronoi' ? this.polygons[this.selectedPolygonIdx] : this.editPolygons[this.selectedPolygonIdx]);

        let timeLine = [];
        for (let i= 0; i < 240; i++) {
            const data = {
                time: i,
                nLIntersections: this.countLineIntersections(this.selectedPolygon),
                nLCrossingPoints: this.crossings.filter(pt => isPointInsidePolygon(pt, this.selectedPolygon)).length,
                nParticles: this.updateParticles(i, false).filter(pt => isPointInsidePolygon(pt, this.selectedPolygon)).length,
                nConflicts: this.countParticleSeparationProblems(this.updateParticles(i, false), this.selectedPolygon),
            };
            timeLine.push(data);
        }
        return timeLine;
    }

    countLineIntersections(polygon) {
        let intersectionCount = 0;

        // Get the polygon's edges (pairs of consecutive vertices)
        const polygonEdges = polygon.map((vertex, index) => {
            const nextVertex = polygon[(index + 1) % polygon.length];
            return [vertex, nextVertex];
        });

        // Check each polyline (+ segments) against all edges of the polygon
        this.lines.forEach(polyline => {
            polygonEdges.forEach(edge => {
                if (doLinesIntersect(polyline, edge)) {
                    intersectionCount++;
                }
            });
        });

        return intersectionCount;
    }

    countParticleSeparationProblems(positions, polygon) {
        let conflicts = 0;
        const problems = this.calculateParticleSeparationProblems(positions);
        problems.forEach(problem => {
            if(isPointInsidePolygon(problem[0],polygon) && isPointInsidePolygon(problem[1],polygon)) {
                conflicts++;
            }
        });

        return conflicts;
    }

    generateWindSwirlData() {
        const data = [];
        const rows = 20, cols = 30;
        const cx = this.width / 2, cy = this.height / 2;
        for (let x = 0; x <= cols; x++) {
            for (let y = 0; y <= rows; y++) {
                const px = (x / cols) * this.width;
                const py = (y / rows) * this.height;
                const dx = px - cx;
                const dy = py - cy;
                const dist = Math.sqrt(dx * dx + dy * dy);
                const angle = Math.atan2(dy, dx) + Math.PI / 2;
                const speed = 1.5 * Math.exp(-dist * dist / 20000);
                data.push({ x: px, y: py, vx: Math.cos(angle) * speed, vy: Math.sin(angle) * speed, speed: speed });
            }
        }
        return data;
    }

   generateWindWavyData() {
        const data = [];
        const rows = 20, cols = 30;
        for (let x = 0; x <= cols; x++) {
            for (let y = 0; y <= rows; y++) {
                const px = (x / cols) * this.width;
                const py = (y / rows) * this.height;
                const angle = Math.sin(px / 80) * Math.PI + Math.cos(py / 100) * Math.PI / 2;
                const speed = 1 + Math.sin(py / 50);
                data.push({ x: px, y: py, vx: Math.cos(angle) * speed, vy: Math.sin(angle) * speed , speed: speed});
            }
        }
        return data;
    }

    generateWindNaturalData(vortexStrength = 0.3) {
        const data = [];
        const rows = 30, cols = 30;

        // Base directional flow (30 degrees NE)
        const baseFlowAngle = Math.PI / 6;
        const baseSpeed = 1.5;

        // Vortex centers
        const vortices = [
            { x: this.width * 0.3, y: this.height * 0.3, direction: -1 },
            { x: this.width * 0.7, y: this.height * 0.6, direction: 1 }
        ];

        for (let xi = 0; xi <= cols; xi++) {
            for (let yi = 0; yi <= rows; yi++) {
                const px = (xi / cols) * this.width;
                const py = (yi / rows) * this.height;

                // Base flow vector
                let vx = Math.cos(baseFlowAngle) * baseSpeed;
                let vy = Math.sin(baseFlowAngle) * baseSpeed;

                // Add vortices with magnitude scaled by vortexStrength
                vortices.forEach(v => {
                    const dx = (px - v.x) * v.direction;
                    const dy = (py - v.y) * v.direction;
                    const distSq = dx * dx + dy * dy + 1e-5;
                    const dist = Math.sqrt(distSq);
                    const angle = Math.atan2(dy, dx) + Math.PI / 2;
                    const strength = vortexStrength * Math.exp(-distSq / 15000);
                    vx += Math.cos(angle) * strength;
                    vy += Math.sin(angle) * strength;
                });

                const speed = Math.sqrt(vx*vx + vy*vy);
                data.push({ x: px, y: py, vx: vx, vy: vy, speed: speed });
            }
        }
        return data;
    }

    generateWindUniformField(speed = 0.8, angle = 0) {
        const data = [];
        const rows = 20, cols = 30;
        const vx = Math.cos(angle) * speed;
        const vy = Math.sin(angle) * speed;
        for (let x = 0; x <= cols; x++) {
            for (let y = 0; y <= rows; y++) {
                const px = (x / cols) * this.width;
                const py = (y / rows) * this.height;
                data.push({ x: px, y: py, vx: vx, vy: vy, speed: speed });
            }
        }
        return data;
    }

   getWindStats(polygon) {
        const points = this.wind_vectors.filter(p =>
           isPointInsidePolygon([p.x, p.y], polygon)
       );

        if(points.length === 0) {
            return null; // no data in area
        }

        // Average vx, vy
        let sumVx = 0, sumVy = 0, sumSpeed = 0;
        const speeds = [];
        const directions = []; // in radians

        points.forEach(p => {
            sumVx += p.vx;
            sumVy += p.vy;
            sumSpeed += p.speed;
            speeds.push(p.speed);

            // direction from vx, vy
            const angle = Math.atan2(p.vy, p.vx);
            directions.push(angle);
        });

        const n = points.length;
        const avgVx = sumVx / n;
        const avgVy = sumVy / n;
        const avgSpeed = sumSpeed / n;

        // Average direction vector to angle
        const avgDirectionRad = Math.atan2(avgVy, avgVx);

        // Circular mean of directions (alternative, but vector average is good)
        // Here we use vector average for avg direction, which is standard

        // Calculate std deviation of speed
        const meanSpeed = avgSpeed;
        const varianceSpeed = speeds.reduce((acc, v) => acc + (v - meanSpeed)**2, 0) / n;
        const stdSpeed = Math.sqrt(varianceSpeed);

        // Calculate circular std deviation of direction
        // Convert angles to unit vectors and find mean resultant length R
        let sumSin = 0, sumCos = 0;
        directions.forEach(a => {
            sumSin += Math.sin(a);
            sumCos += Math.cos(a);
        });
        const meanSin = sumSin / n;
        const meanCos = sumCos / n;
        const R = Math.sqrt(meanSin*meanSin + meanCos*meanCos);

        // Circular std dev formula: sqrt(-2 * ln(R))
        // Convert to degrees
        const stdDirectionRad = Math.sqrt(-2 * Math.log(R));
        const stdDirectionDeg = stdDirectionRad * 180 / Math.PI;

        return {
            avgSpeed,
            avgDirectionDeg: (avgDirectionRad * 180 / Math.PI + 360) % 360, // 0-360 degrees
            stdSpeed,
            stdDirectionDeg
        };
    }

    update_wind() {

        if(this.wind_loaded && this.wind_vectors.length !== 0) return;

        this.wind_vectors = [];
        this.wind_loaded = false;

        this.wind_type = envState.wind;

        if (this.wind_type === "swirl") {this.wind_vectors = this.generateWindSwirlData(); this.wind_loaded = true;}
        else if (this.wind_type === "wavy") {this.wind_vectors = this.generateWindWavyData(); this.wind_loaded = true;}
        else if (this.wind_type === "natural") {this.wind_vectors = this.generateWindNaturalData(2.0); this.wind_loaded = true;}
        else if (this.wind_type === "uniform") {this.wind_vectors = this.generateWindUniformField(2, Math.PI / 4); this.wind_loaded = true;}
        else if (this.wind_type === "perlin") {
            const rows = 25, cols = 40;
            for (let x = 0; x <= cols; x++) {
                for (let y = 0; y <= rows; y++) {
                    const px = (x / cols) * this.width;
                    const py = (y / rows) * this.height;
                    // Use Perlin noise to generate angle & magnitude
                    const angle = Perlin.noise(px * 0.01, py * 0.01) * Math.PI * 2;
                    const speed = 2 + Perlin.noise(px * 0.02 + 100, py * 0.02 + 100) * 2;
                    const vx = Math.cos(angle) * speed;
                    const vy = Math.sin(angle) * speed;
                    this.wind_vectors.push({ x: px, y: py, vx: vx, vy: vy, speed: speed });
                    this.wind_loaded = true;
                }
            }
        } else if (this.wind_type === "simplex") {
            const rows = 25, cols = 40;
            for (let x = 0; x <= cols; x++) {
                for (let y = 0; y <= rows; y++) {
                    const px = (x / cols) * this.width;
                    const py = (y / rows) * this.height;
                    const angle = Simplex.noise(px * 0.01, py * 0.01) * Math.PI * 2;
                    const speed = 2 + Simplex.noise(px * 0.02 + 100, py * 0.02 + 100) * 2;
                    const vx = Math.cos(angle) * speed;
                    const vy = Math.sin(angle) * speed;
                    this.wind_vectors.push({x: px, y: py, vx: vx, vy: vy, speed: speed});
                    this.wind_loaded = true;
                }
            }
        } else {
            // load from a file
            this.loadWindFieldFromFile(this.wind_type);
        }
    }

    loadWindFieldFromFile(url) {
        fetch(url)
            .then(response => {
                if (!response.ok) throw new Error(`HTTP error ${response.status}`);
                return response.json();
            })
            .then(data => {
                // only set the wind "loaded" boolean and force a redraw
                // AFTER the wind_vectors are fully loaded and filled with "data"
                this.wind_vectors = data;
                this.wind_loaded = true;
                renderUtils.renderAll();
            })
            .catch(err => {
                console.error("Failed to load wind field:", err);
                alert("Could not load wind field.");
            });
    }

    saveAsJSON() {
        const data = {
            dimension: {width: this.width, height: this.height},
            mode: envState.editMode,
            numVoronoiPoints: this.voronoiPoints.length,
            numLines: this.lines.length,
            numLineParticles: this.numberOfParticles,
            numParticles: this.particles.length,
            pathFinder: envState.pathFinder,
            gridSize: this.cellSize,
            voronoiPoints: this.voronoiPoints,
            voronoiPolygons: this.polygons,
            voronoiVertices: this.voronoiVertices,
            editPolygons: this.editPolygons,
            polygonVertices: this.editVertices,
            polygonIndices: this.editPolygonIndices,
            clippingPolygon: this.clippingPolygon,
            lineCrossings: this.crossings,
            lines: this.lines,
            particles: this.particles,
            blockedCells: [...this.selectedCells]
        };

        const jsonBlob = new Blob([JSON.stringify(data, null, 2)], {type: "application/json"});
        const url = URL.createObjectURL(jsonBlob);

        const a = document.createElement("a");
        a.href = url;
        const filename = "V" + this.voronoiPoints.length + "L" + this.lines.length + "P" + this.numberOfParticles + "_" + new Date().toISOString() + ".json";
        a.download = filename;
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        URL.revokeObjectURL(url);
    }
    
    update() {
        if (envState.editMode === 'voronoi') this.computeVoronoi();
        this.updateEditPolygons();
        this.updateHeatMap();

        this.crossings = calculateLineCrossings(this.lines);

        // retrieve constraint violations
        this.problemLinePolygonCrossings = getProblematicLineCrossings((envState.editMode === 'voronoi' ? this.polygons : this.editPolygons), this.crossings, envState.distance_threshold);
        [this.intersectionPoints, this.problemSegments] = calculateIntersections(this.lines, (envState.editMode === 'voronoi' ? this.polygons : this.editPolygons), envState.length_threshold);
        this.problemVertices = getProblematicVertices((envState.editMode === 'voronoi' ? this.voronoiVertices : this.editVertices) , this.intersectionPoints, envState.vertex_line_threshold);
        this.numLinePolygonIntersections = this.intersectionPoints.length;

        this.update_wind();

    }

    reset() {
        this.wind_vectors = [];
        this.wind_loaded = false;
    }

}