const { BinaryHeap } = require("./binaryHeap");

// Mesh: {width: width, height: height, size: size, blockedIndices: blockedIndices}
class Astar {
    constructor(mesh, heuristic) {
        this.size = mesh.size;                            // Cell size
        this.heuristicName = heuristic;                   // Heuristic method
        this.graph = new Graph(mesh);                     // Graph-related operations
        this.openHeap = new BinaryHeap(node => node.f);   // Generate binary heap instead of list
    }

    // search the path
    search(start, end) {
        let startGrid = start.map(e => Math.max(0, e / this.size - 10e-8));
        let endGrid = end.map(e => Math.max(0, e / this.size - 10e-8));

        // Initialization
        startGrid = [Math.floor(startGrid[0]), Math.floor(startGrid[1])];
        endGrid = [Math.floor(endGrid[0]), Math.floor(endGrid[1])];

        const startNode = this.graph.grids[startGrid[0]][startGrid[1]];
        const endNode = this.graph.grids[endGrid[0]][endGrid[1]];

        // Set the start and end coordinates
        [startNode.x, startNode.y] = [start[0] / this.size, start[1] / this.size];
        [endNode.x, endNode.y] = [end[0] / this.size, end[1] / this.size];

        startNode.h = this.heuristic(startNode, endNode);
        startNode.f = startNode.h;
        this.openHeap.push(startNode);

        // Search loop
        while (this.openHeap.size() > 0) {

            // Grab the lowest f(x) to process next.  Heap keeps this sorted.
            const currentNode = this.openHeap.shift();

            // Normal case -- move currentNode from open to closed, process each of its neighbors.
            currentNode.closed = true;

            // End case -- result has been found, return the traced path.
            if (currentNode === endNode) {
                const path = this.pathTo(endNode);
                this.resetNodes(startNode, endNode);
                this.openHeap.content = [];
                return path;
            }

            // Find all neighbors for the current node.
            const neighbors = this.graph.findNeighbors(currentNode);

            for (let i = 0; i < neighbors.length; i++) {
                const neighbor = neighbors[i];

                // Not a valid node to process, skip to next neighbor.
                if (neighbor.closed || neighbor.isWall() || this.graph.isStumble(currentNode, neighbor)) continue;

                // Find the parent of the neighbor
                const parent = this.findNeighborParent(currentNode, neighbor);

                // The g score is the shortest distance from start to current node.
                // We need to check if the path we have arrived at this neighbor is the shortest one we have seen yet.
                const gScore = parent.g + this.euclidean(parent, neighbor);
                const beenVisited = neighbor.visited;

                if (!beenVisited || gScore < neighbor.g) {

                    // Found an optimal (so far) path to this node.  Take score for node to see how good it is.
                    neighbor.visited = true;
                    neighbor.parent = parent;
                    neighbor.h = this.heuristic(neighbor, endNode);
                    neighbor.g = gScore;
                    neighbor.f = neighbor.g + neighbor.h;

                    if (!beenVisited) // Pushing to heap will put it in proper place based on the 'f' value.
                        this.openHeap.push(neighbor);
                    else // Already seen the node, but since it has been rescored we need to reorder it in the heap
                        this.openHeap.sortElement(neighbor);
                }
            }
        }

        // Fail to find a path.
        this.resetNodes(startNode, endNode)
        this.openHeap.content = [];
        return [];
    }

    resetNodes(startNode, endNode){
        this.graph.nodes.forEach(node=>{
            node.f = 0;
            node.g = 0;
            node.h = 0;
            node.visited = false;
            node.closed = false;
            node.parent = null;
        });
        this.resetNode(startNode);
        this.resetNode(endNode);
    }

    findNeighborParent(currentNode) {
        return currentNode;
    }

    pathTo(node, end) {
        let curr = node;
        const path = end ? [end] : [];
        while (curr) {
            path.unshift(curr);
            curr = curr.parent;
        }
        return path.map(n => [n.x * this.size, n.y * this.size]);
    }

    resetNode(node) {
        [node.x, node.y] = [Math.floor(node.x) + 0.5, Math.floor(node.y) + 0.5];
    }

    heuristic(node1, node2) {
        if (node1 === node2) return 0;
        if (this.heuristicName === 'euclidean')
            return this.euclidean(node1, node2);
        if (this.heuristicName === 'diagonal') {
            const d1 = Math.abs(node1.x - node2.x);
            const d2 = Math.abs(node1.y - node2.y);
            // -0.585787 = sqrt(2) - 2
            // A litte trick to make the grid path more straight
            const distance = (d1 + d2) - 0.585787 * Math.min(d1, d2);
            return distance;
        }
        return 0;
    }

    euclidean(node1, node2) {
        const d1 = Math.abs(node2.x - node1.x);
        const d2 = Math.abs(node2.y - node1.y);
        return Math.sqrt(d1 * d1 + d2 * d2);
    }
}

class Node {
    constructor(point, weight) {
        this.x = point[0];
        this.y = point[1];
        this.weight = weight;
        this.f = 0;
        this.g = 0;
        this.h = 0;
        this.visited = false;
        this.closed = false;
        this.parent = null;
    }

    isWall() {
        // zero weight means wall
        return this.weight === 0;
    }
}

class Graph {
    constructor(mesh) {
        this.mesh = mesh;  // Storing mesh
        this.nodes = [];   // 1D array for storing nodes
        this.grids = [];   // 2D array for storing nodes
        this.init();
    }

    // Initialize the nodes
    init() {

        const width = this.mesh.width;
        const height = this.mesh.height;
        const size = this.mesh.size;
        const blockedIndices = this.mesh.blockedIndices;

        let cellIndex = 0;
        for (let x = 0; x < width / size; x++) {
            this.grids[x] = [];
            for (let y = 0; y < height / size; y++) {
                const weight = blockedIndices.includes(cellIndex++) ? 0 : 1;
                // [x + 0.5, y + 0.5]: the center of the grid
                // like http://playtechs.blogspot.com/2007/03/raytracing-on-grid.html
                // Easy for line-of-sight checks
                const node = new Node([x + 0.5, y + 0.5], weight);
                this.grids[x][y] = node;
                this.nodes.push(node);
            }
        }
    }

    findNeighbors(node) {
        const neighbors = [];
        const x = Math.floor(node.x);
        const y = Math.floor(node.y);
        const grids = this.grids;

        // Eight neighbors.
        if (grids[x - 1] && grids[x - 1][y]) neighbors.push(grids[x - 1][y]);
        if (grids[x + 1] && grids[x + 1][y]) neighbors.push(grids[x + 1][y]);
        if (grids[x] && grids[x][y - 1]) neighbors.push(grids[x][y - 1]);
        if (grids[x] && grids[x][y + 1]) neighbors.push(grids[x][y + 1]);
        if (grids[x - 1] && grids[x - 1][y - 1]) neighbors.push(grids[x - 1][y - 1]);
        if (grids[x + 1] && grids[x + 1][y - 1]) neighbors.push(grids[x + 1][y - 1]);
        if (grids[x - 1] && grids[x - 1][y + 1]) neighbors.push(grids[x - 1][y + 1]);
        if (grids[x + 1] && grids[x + 1][y + 1]) neighbors.push(grids[x + 1][y + 1]);

        return neighbors;
    }

    isStumble(node, neighbor) {
        // No way to go through the junction of two obstacle
        const [x1, y1] = [Math.floor(node.x), Math.floor(node.y)];
        const [x2, y2] = [Math.floor(neighbor.x), Math.floor(neighbor.y)];
        const grids = this.grids;
        if (Math.abs(x1 - x2) === 1 && Math.abs(y1 - y2) === 1) {
            if ((!grids[x1] || !grids[x1][y2] || grids[x1][y2].isWall()) &&
                (!grids[x2] || !grids[x2][y1] || grids[x2][y1].isWall()))
                return true;
        }
        return false;
    }

    // Check if the line segment connecting node1 and node2 intersects any obstacle / boundary in the environment
    lineOfSightGrid(node1, node2) {
        if (!node1 || !node2) return false;
        return lineOfSightGrid(node1.x, node1.y, node2.x, node2.y, this.grids);
    }
}

function lineOfSightGrid(x0, y0, x1, y1, grids) {

    const isBlocked = (x, y) => {
        // if (!grids[x] || !grids[x][y] || !grids[x][y].weight)
        //     return true;
        if (grids[x] && grids[x][y] && !grids[x][y].weight)
            return true;
        return false;
    };

    const dx = Math.abs(x1 - x0);
    const dy = Math.abs(y1 - y0);

    let x = Math.floor(x0);
    let y = Math.floor(y0);

    // The start point is blocked
    if (!Number.isInteger(x0) && !Number.isInteger(y0) && isBlocked(x, y)) return false;
    if (Number.isInteger(x0) && Number.isInteger(y0) && x1 > x0 && y1 > y0 && isBlocked(x0, y0)) return false;

    let n = 1;         // The number of intersecting grids.
    let x_inc, y_inc;  // The increment (direction) of x and y.
    let error;         // Determine horizontal or vertical expansion.

    // If the slope is infinity
    if (dx === 0) {
        x_inc = 0;
        error = Infinity;
    }
    else if (x1 > x0) {
        x_inc = 1;
        n += Math.floor(x1) - x;
        error = (x + 1 - x0) * dy;
    }
    else {
        x_inc = -1;
        n += x - Math.floor(x1);
        error = (x0 - x) * dy;
    }

    if (dy === 0) {
        y_inc = 0;
        error = -Infinity;
    }
    else if (y1 > y0) {
        y_inc = 1;
        n += Math.floor(y1) - y;
        error -= (y + 1 - y0) * dx;
    }
    else {
        y_inc = -1;
        n += y - Math.floor(y1);
        error -= (y0 - y) * dx;
    }

    while (--n > 0) {
        if (error > 0) {
            y += y_inc;
            error -= dx;
        }
        else if (error < 0) {
            x += x_inc;
            error += dy;
        }
        else {
            if (isBlocked(x + x_inc, y) &&
                isBlocked(x, y + y_inc))
                return false;
            x += x_inc;
            error += dy;
            y += y_inc;
            error -= dx;
            n--;
        }
        if (isBlocked(x, y)) return false;
    }
    return true;
}

module.exports = { Astar };