// a collection of geometry utility functions that can be called
// from any class

export function getTrackLocationAtDistance(track, distance) {
    let traveled = 0;
    for (let i = 1; i < track.length; i++) {
        const segmentLength = getLineSegmentLength(track[i-1], track[i]);
        if (traveled + segmentLength >= distance) {
            const ratio = (distance - traveled) / segmentLength;
            const x = track[i - 1][0] + ratio * (track[i][0] - track[i - 1][0]); // interpolate x position
            const y = track[i - 1][1] + ratio * (track[i][1] - track[i - 1][1]); // interpolate y position
            return [x,y];
        }
        traveled += segmentLength;
    }
    return track[track.length - 1];
}

export function getLineSegmentLength(p1, p2) {
    const x1 = p1[0], y1 = p1[1],
        x2 = p2[0], y2 = p2[1];
    return Math.sqrt(Math.pow(x2 - x1, 2) + Math.pow(y2 - y1, 2));
}

// Helper function to check intersection between two line segments using Vector Cross Product
export function getIntersection(p1, p2, p3, p4) {
    const x1 = p1[0], y1 = p1[1],
        x2 = p2[0], y2 = p2[1],
        x3 = p3[0], y3 = p3[1],
        x4 = p4[0], y4 = p4[1];

    // Compute determinants
    const denom = (x1 - x2) * (y3 - y4) - (y1 - y2) * (x3 - x4);
    if (Math.abs(denom) < 1e-10) return null;  // Parallel or coincident lines

    const t = ((x1 - x3) * (y3 - y4) - (y1 - y3) * (x3 - x4)) / denom;
    const u = ((x1 - x3) * (y1 - y2) - (y1 - y3) * (x1 - x2)) / denom;

    // Check if intersection is within both line segments
    if (t >= 0 && t <= 1 && u >= 0 && u <= 1) {
        const ix = x1 + t * (x2 - x1);
        const iy = y1 + t * (y2 - y1);
        return [ix, iy];
    }

    return null; // No valid intersection
}

// Check if a point is inside a polygon using ray-casting algorithm
export function isPointInsidePolygon(point, polygon) {
    let [px, py] = point;
    let inside = false;
    for (let i = 0, j = polygon.length - 1; i < polygon.length; j = i++) {
        let [x1, y1] = polygon[i];
        let [x2, y2] = polygon[j];
        let intersect = ((y1 > py) !== (y2 > py)) &&
            (px < (x2 - x1) * (py - y1) / (y2 - y1) + x1);
        if (intersect) inside = !inside;
    }
    return inside;
}

// Calculate the area of a polygon using Shoelace formula
export function calculatePolygonArea(polygon) {
    let area = 0;
    for (let i = 0, j = polygon.length - 1; i < polygon.length; j = i++) {
        area += (polygon[j][0] + polygon[i][0]) * (polygon[j][1] - polygon[i][1]);
    }
    return Math.abs(area / 2);
}

// calculate the centroid of a polygon, uses the Shoelace algorithm
export function polygonCentroid(polygon) {
    let area = 0;
    let cx = 0;
    let cy = 0;
    const n = polygon.length;

    for (let i = 0; i < n; i++) {
        const [x0, y0] = polygon[i];
        const [x1, y1] = polygon[(i + 1) % n];
        const a = x0 * y1 - x1 * y0;
        area += a;
        cx += (x0 + x1) * a;
        cy += (y0 + y1) * a;
    }

    area *= 0.5;
    if (area === 0) return polygon[0]; // Degenerate case

    cx /= (6 * area);
    cy /= (6 * area);

    return [cx, cy];
}

// calculate the interaction angle between two line segments
export function calculateAngleBetweenLineSegments(line1, line2) {
    function getVector(line) {
        let [p1, p2] = line;
        return { x: p2[0] - p1[0], y: p2[1] - p1[1] };
    }

    let v1 = getVector(line1);
    let v2 = getVector(line2);

    let magnitude1 = Math.sqrt(v1.x ** 2 + v1.y ** 2);
    let magnitude2 = Math.sqrt(v2.x ** 2 + v2.y ** 2);

    if (magnitude1 === 0 || magnitude2 === 0) return -1; // Avoid division by zero

    let dotProduct = v1.x * v2.x + v1.y * v2.y;
    let cosineTheta = dotProduct / (magnitude1 * magnitude2);
    let angleRad = Math.acos(Math.min(1, Math.max(-1, cosineTheta))); // Clamp to avoid precision errors

    return (angleRad * 180) / Math.PI;
}

// Calculate crossing points between all polylines
export function calculateLineCrossings(lines) {
    const crossings = [];
    for (let i = 0; i < lines.length; i++) {
        for (let j = 0; j < lines[i].length - 1; j++) {
            for (let k = i + 1; k < lines.length; k++) {
                for (let l = 0; l < lines[k].length - 1; l++) {
                    const intersection = getIntersection(
                        lines[i][j], lines[i][j + 1],
                        lines[k][l], lines[k][l + 1]
                    );
                    if (intersection) {
                        crossings.push(intersection);
                    }
                }
            }
        }
    }
    return crossings;
}

// Function to check if a polyline segment and polygon edge intersect
export function doLinesIntersect(polyline, edge) {

    for (let i = 0; i < polyline.length -1 ; i++) {

        const p1 = polyline[i];
        const p2 = polyline[i+1];
        const [q1, q2] = edge;

        const cross = (a, b, c) => (b[0] - a[0]) * (c[1] - a[1]) - (b[1] - a[1]) * (c[0] - a[0]);

        const d1 = cross(p1, p2, q1);
        const d2 = cross(p1, p2, q2);
        const d3 = cross(q1, q2, p1);
        const d4 = cross(q1, q2, p2);

        // Check general case
        if (d1 * d2 < 0 && d3 * d4 < 0) {
            return true;
        }

        // Check for special colinear cases (overlapping segments)
        const isOnSegment = (a, b, c) =>
            Math.min(a[0], b[0]) <= c[0] && c[0] <= Math.max(a[0], b[0]) &&
            Math.min(a[1], b[1]) <= c[1] && c[1] <= Math.max(a[1], b[1]);

        if (d1 === 0 && isOnSegment(p1, p2, q1)) return true;
        if (d2 === 0 && isOnSegment(p1, p2, q2)) return true;
        if (d3 === 0 && isOnSegment(q1, q2, p1)) return true;
        if (d4 === 0 && isOnSegment(q1, q2, p2)) return true;
    }
    return false;
}


// calculate the shortest distance between a point and a line
export function pointToLineDistance(point, line) {
    const [x0, y0] = point;
    const [[x1, y1], [x2, y2]] = line;

    const dx = x2 - x1;
    const dy = y2 - y1;
    const lengthSquared = dx * dx + dy * dy;

    if (lengthSquared === 0) {
        // The line segment is actually a point
        return Math.sqrt((x0 - x1) ** 2 + (y0 - y1) ** 2);
    }

    // Project point onto the line segment
    let t = ((x0 - x1) * dx + (y0 - y1) * dy) / lengthSquared;
    t = Math.max(0, Math.min(1, t)); // Clamp t to the segment range

    const nearestX = x1 + t * dx;
    const nearestY = y1 + t * dy;

    // Compute distance from the point to the nearest point on the segment
    return Math.sqrt((x0 - nearestX) ** 2 + (y0 - nearestY) ** 2);
}


// calculate the unit direction vector between two points
export function getUnitDirectionVector(start, end) {
    // Calculate the difference in x and y coordinates
    let dx = end[0] - start[0];
    let dy = end[1] - start[1];

    // Calculate the magnitude (length) of the vector
    let magnitude = Math.sqrt(dx * dx + dy * dy);

    // Prevent division by zero
    if (magnitude === 0) {
        throw new Error("The two points are the same; direction is undefined.");
    }

    // Compute the unit direction vector
    //return { ux: dx / magnitude, uy: dy / magnitude };
    return [ dx/magnitude, dy/magnitude ];
}

// calculate the additional distance between direct and indirect polylines
export function calculateAdditionalLineLength(polyLines) {
    const additional_length = [];
    polyLines.forEach(polyline => {
        const start = polyline[0];
        const end = polyline[polyline.length - 1];
        const direct = getLineSegmentLength(start,end);

        let polyLength = 0;
        for (let i = 0; i < polyline.length - 1; i++) {
            polyLength += getLineSegmentLength(polyline[i],polyline[i+1]);
        }
        additional_length.push(polyLength - direct);
    });
    return additional_length;
}

// get the vertices of points that are too close to a polygon edge
export function getProblematicLineCrossings(polygons, points, threshold) {
    const problematicPoints = [];

    points.forEach(pt => {
        polygons.forEach(polygon => {
            for (let i = 0; i < polygon.length; i++) {
                const segmentStart = polygon[i];
                const segmentEnd = polygon[(i + 1) % polygon.length];

                if (pointToLineDistance(pt, [segmentStart, segmentEnd]) < threshold) {
                    problematicPoints.push(pt);
                }
            }
        });
    });

    return problematicPoints;
}

// Calculate intersections between random lines and Voronoi polygons,
// returns [xpos,ypos,intersectionAngle_deg]
export function calculateIntersections(lines, polygons, minLength) {
    const allIntersections = [];
    const problematicSegments = [];

    lines.forEach(line => {
        const lineIntersections = [];
        for (let i = 0; i < line.length - 1; i++) {
            const lineStart = line[i];
            const lineEnd = line[i + 1];
            polygons.forEach(polygon => {
                for (let i = 0; i < polygon.length; i++) {
                    const segmentStart = polygon[i];
                    const segmentEnd = polygon[(i + 1) % polygon.length];

                    const intersection = getIntersection(lineStart, lineEnd, segmentStart, segmentEnd);
                    if (intersection) {
                        const angle = calculateAngleBetweenLineSegments([lineStart,lineEnd],[segmentStart,segmentEnd]);
                        allIntersections.push([intersection[0], intersection[1], angle]);
                        lineIntersections.push(intersection);
                    }
                }
            });
        }
        // determine problematic line lengths
        for (let i = 0; i < lineIntersections.length; i++) {
            const intersectionStart = lineIntersections[i];
            const nextIntersection = lineIntersections[(i + 1) % lineIntersections.length];
            if (getLineSegmentLength(intersectionStart, nextIntersection) < minLength) {
                problematicSegments.push([intersectionStart,nextIntersection]);
            }
        }
    });

    return [allIntersections, problematicSegments];
}

// get the problematic line vertex that is too close to a polygon vertex
export function getProblematicVertices(polygonVertices,points,vertex_line_threshold) {
    const lineVertices = [];

    polygonVertices.forEach(vertex => {
        points.forEach(pt => {
            if(getLineSegmentLength(vertex,pt) <= vertex_line_threshold) {
                lineVertices.push(pt);
            }
        });
    });

    return lineVertices;
}





