import * as d3 from 'd3';
import { envState } from "../settings";
import { highlightUtils } from '../newmain.js';
import { renderUtils } from '../newmain.js';
import * as canvg from "canvg";
import '../styles.css';
import {kmeansStability} from "../clustering/kmeans";
import {easeInOutCubic} from "../utility/animation";

export class World {
    constructor(environment) {
        this.environment = environment;
        this.svgCanvas = undefined;
        this.svg = undefined;
        this.zoom = undefined;
        this.tooltip = undefined;
        this.contextMenu = undefined;
        this.svgWidth = 800;
        this.svgHeight = 600;
        this.draggingPoint = null;
        this.draggingPolygonPoint = null;
        this.selectedPoint = null;
        this.hoveredGridCell = null;

        highlightUtils.setInstance("world", this);
        renderUtils.setInstance("world", this);
    }

    init() {

        // Create tooltip if not already created
        if (!this.tooltip) {
            this.tooltip = d3.select("body")
                .append("div")
                .attr("class", "tooltip");
        }

        // Create SVG canvas if not already created
        if (!this.svgCanvas) {
            this.svgCanvas = d3.select("#voronoi-container")
                .append("svg")
                .attr("id", "voronoiCanvas")
                .style("border", "1px solid #ccc")
                .style("margin-bottom", "8px");

            this.svg = this.svgCanvas.append("g");
        }

        // Always update size and viewBox in case environment changed
        this.svgCanvas
            .attr("width", this.svgWidth)
            .attr("height", this.svgHeight)
            .attr("viewBox", `0 0 ${this.environment.width} ${this.environment.height}`)
            .attr("preserveAspectRatio", "xMidYMid meet");

        // Create zoom behavior only once
        if (!this.zoom) {
            this.zoom = d3.zoom()
                .scaleExtent([0.25, 3.0])
                .translateExtent([[0, 0], [this.environment.width, this.environment.height]])
                .on("zoom", (event) => {
                    this.svg.attr("transform", event.transform);
                });
        } else {
            // Update translateExtent in case environment changed
            this.zoom.translateExtent([[0, 0], [this.environment.width, this.environment.height]]);
        }

        // Apply zoom behavior to this.svg (the <g> group)
        this.svg.call(this.zoom);
    }

    initEventHandlers() {
        // attach event handlers

        this.contextMenu = d3.select("#context-menu");

        d3.select("body").on("click", () => this.hideContextMenu());

        this.svg.on("mousemove", (event) => {
            if (!envState.isCtrlPressed || envState.activeTab !== 'environment') return;

            const [mouseX, mouseY] = d3.pointer(event, this.svg.node());
            const cell = this.environment.getNearestCell(mouseX, mouseY);

            if (cell && this.hoveredGridCell !== cell) {
                //svg.selectAll("rect").attr("fill", d => selectedCells.has(d.index) ? "orange" : "none");
                this.drawGridCell();
                d3.selectAll("rect").filter(d => d.x === cell.x && d.y === cell.y).attr("fill", "lightblue");
                this.hoveredGridCell = cell;
            }

            if (envState.isDragging && cell && !this.environment.selectedCells.has(cell.index)) {
                this.environment.selectedCells.add(cell.index);
                this.drawGridCell();
                //d3.selectAll("rect").filter(d => d.index === cell.index).attr("fill", "orange");
            }

            if (envState.isRightDragging && cell && this.environment.selectedCells.has(cell.index)) {
                this.environment.selectedCells.delete(cell.index);
                //d3.selectAll("rect").filter(d => d.index === cell.index).attr("fill", "none");
                this.drawGridCell();
            }
        });

        this.svg.on("mousedown", (event) => {
            if (!envState.isCtrlPressed || envState.activeTab !== 'environment') return;
            envState.isDragging = true;

            const [mouseX, mouseY] = d3.pointer(event, this.svg.node());
            const cell = this.environment.getNearestCell(mouseX, mouseY);

            if (event.button === 0) { // Left mouse button for selecting
                envState.isDragging = true;
                if (cell) {
                    this.environment.selectedCells.add(cell.index);
                    this.drawGridCell();
                }
            } else if (event.button === 2) { // Right mouse button for deselecting
                envState.isRightDragging = true;
                if (cell) {
                    this.environment.selectedCells.delete(cell.index);
                    this.drawGridCell();
                }
            }
        });

        this.svg.on("mouseup", function () {
            envState.isDragging = false;
            envState.isRightDragging = false;
        });

        // Prevent context menu from appearing on right-click
        this.svg.on("contextmenu", (event) => {
            [envState.mouseXPos, envState.mouseYPos] = d3.pointer(event, this.svg.node());
            event.preventDefault();
            if (!envState.isCtrlPressed) this.showContextMenu('global', event.pageX, event.pageY);
        });
    }

    async zoomToPoint(x, y, scale) {
        const viewBox = this.svgCanvas.attr("viewBox").split(" ").map(Number);
        const vbWidth = viewBox[2];
        const vbHeight = viewBox[3];

        // Compute translation to center the (x, y) point
        const translateX = vbWidth / 2 - x * scale;
        const translateY = vbHeight / 2 - y * scale;

        const t = this.svg.transition().duration(750);

        // Apply transform
        t.call(this.zoom.transform,
            d3.zoomIdentity
                .translate(translateX, translateY)
                .scale(scale)
        );

        await t.end();
    }

    async zoomToArea(x, y, width, height, scale) {
        const viewBox = this.svgCanvas.attr("viewBox").split(" ").map(Number);
        const vbWidth = viewBox[2];
        const vbHeight = viewBox[3];

        // Compute center of the area to zoom to
        const centerX = x + width / 2;
        const centerY = y + height / 2;

        // Translate so that center of area appears in center of viewBox
        const translateX = vbWidth / 2 - centerX * scale;
        const translateY = vbHeight / 2 - centerY * scale;

        const t = this.svg.transition().duration(750);

        // Apply transform
        t.call(this.zoom.transform,
            d3.zoomIdentity
                .translate(translateX, translateY)
                .scale(scale)
        );

        await t.end();
    }

    drawEmptySVG() {
        this.svg.selectAll(".empty").remove(); // Remove old heatmap

        // Draw grid cells
        this.svg.selectAll(".empty")
            .data([0, 0])
            .enter()
            .append("rect")
            .attr("class", "empty")
            .attr("x", d => d[0])
            .attr("y", d => d[1])
            .attr("width", this.environment.width)
            .attr("height", this.environment.height)
            .attr("fill", "rgba(228,227,227,0.1)")
            .attr("stroke", "none");
    }

    drawDelaunayGraph() {

        // Draw Delaunay lines
        const delaunay = d3.Delaunay.from(this.environment.voronoiPoints);
        const triangles = delaunay.trianglePolygons();

        this.svg.selectAll(".delaunay-edges").remove(); // Remove old

        this.svg.selectAll("delaunay-edges")
            .data(triangles)
            .enter()
            .append("path")
            .attr("class", "delaunay-edges")
            .attr("d", d => "M" + d.join("L") + "Z")
            .attr("stroke", "#000")
            .attr("fill", "none");

        this.svg.selectAll(".graphcontrol-point").remove(); // Remove old

        // Draw Voronoi Points
        this.svg.selectAll(".graphcontrol-point")
            .data(this.environment.voronoiPoints)
            .enter().append("circle")
            .attr("class", "graphcontrol-point")
            .attr("cx", d => d[0])
            .attr("cy", d => d[1])
            .attr("r", 6)
            .attr("fill", "green")
            .attr("stroke", "rgba(66,74,41,0.7)")
            .attr("stroke-width", 2)
            .call(d3.drag()
                .on("start", (_, d) => {
                    if (!envState.isCtrlPressed) {
                        this.draggingPoint = this.environment.voronoiPoints.findIndex(p => p[0] === d[0] && p[1] === d[1]);
                    }
                })
                .on("drag", (event) => {
                    if (this.draggingPoint !== null && !envState.isCtrlPressed) {
                        this.environment.voronoiPoints[this.draggingPoint] = [Math.max(0, Math.min(this.environment.width, event.x)), Math.max(0, Math.min(this.environment.height, event.y))];
                        //requestAnimationFrame(render);
                        this.environment.update();
                        renderUtils.renderAll();
                        //computeVoronoi();
                        //render();
                    }
                })
                .on("end", () => {
                    if (this.draggingPoint !== null && !envState.isCtrlPressed) {
                        // socket.emit("updatePoint", {
                        //     index: draggingPoint,
                        //     x: points[draggingPoint][0],
                        //     y: points[draggingPoint][1]
                        // });
                        this.draggingPoint = null;
                    }
                })
            )
            .on("contextmenu", (event, d) => {
                if (!envState.isCtrlPressed) {
                    event.preventDefault();
                    this.selectedPoint = d;
                    event.stopPropagation(); // Prevent SVG context menu from showing
                    [envState.mouseXPos, envState.mouseYPos] = d3.pointer(event, this.svg.node());
                    this.showContextMenu('element', event.pageX, event.pageY);
                }
            });
    }

    drawVoronoi() {
        let index = -1;
        this.svg.selectAll(".edit-polygon").remove(); // Remove old

        this.svg.selectAll(".polygon").remove(); // Remove old
        this.svg.selectAll(".voronoi-vertex").remove();
        this.svg.selectAll(".voronoi-point").remove();

        const self = this;

        // Draw Voronoi Polygons (Clipped)
        this.svg.selectAll(".polygon")
            .data(this.environment.polygons)
            .enter().append("polygon")
            .attr("class", "polygon")
            .attr("points", d => d ? d.map(p => p.join(",")).join(" ") : "")
            //.attr("fill", "rgba(225,225,225,0.7)")
            .attr("fill", (d, i) => i === this.environment.selectedPolygonIdx ? "rgba(145,180,227,0.6)" : "rgba(225,225,225,0.7)")
            .attr("stroke", "black")
            .attr("stroke-width", 2.0)
            .attr("data-index", (d, i) => i)
            .on("mouseover", function (event, d) {
                if (!envState.isCtrlPressed) {
                    //const area = calculatePolygonArea(d);
                    //const numIntersections = intersectionPoints.filter(pt => isPointInsidePolygon(pt, d)).length;

                    // Count line crossing points inside the polygon
                    //const numCrossings = crossings.filter(pt => isPointInsidePolygon(pt, d)).length;

                    index = self.environment.polygons.indexOf(d);

                    d3.select(this).attr("fill", "rgba(255,165,0,0.6)"); // Highlight with orange
                    self.tooltip.style("visibility", "visible")
                        .text(`Cell: ${index + 1}`);

                    highlightUtils.highlightBar(index);
                } else {
                    self.tooltip.style("visibility", "hidden");
                }
            })
            .on("mousemove", function (event) {
                if (!envState.isCtrlPressed) {
                    self.tooltip.style("top", `${event.pageY + 10}px`)
                        .style("left", `${event.pageX + 10}px`);
                    highlightUtils.highlightBar(index);
                } else {
                    self.tooltip.style("visibility", "hidden");
                }
            })
            .on("mouseout", function () {
                if (!envState.isCtrlPressed) {
                    self.tooltip.style("visibility", "hidden");
                    if(index !== self.environment.selectedPolygonIdx)
                        d3.select(this).attr("fill", "rgba(225,225,225,0.7)"); // Reset
                    else
                        d3.select(this).attr("fill", "rgba(145,180,227,0.6)"); // Reset
                    highlightUtils.removeHighlightBar(index);
                } else {
                    self.tooltip.style("visibility", "hidden");
                }
            });

        // Draw Voronoi Vertices (Green Dots)
        this.svg.selectAll(".voronoi-vertex")
            .data(this.environment.voronoiVertices)
            .enter().append("circle")
            .attr("class", "voronoi-vertex")
            .attr("cx", d => d[0])
            .attr("cy", d => d[1])
            .attr("r", 3)
            .attr("fill", "green");

        // Draw Voronoi Points (Red)
        this.svg.selectAll(".voronoi-point")
            .data(this.environment.voronoiPoints)
            .enter().append("circle")
            .attr("class", "voronoi-point")
            .attr("cx", d => d[0])
            .attr("cy", d => d[1])
            .attr("r", 6)
            .attr("fill", "green")
            .attr("stroke", "rgba(66,74,41,0.7)")
            .attr("stroke-width", 2)
            .call(d3.drag()
                .on("start", (_, d) => {
                    if (!envState.isCtrlPressed) {
                        this.draggingPoint = this.environment.voronoiPoints.findIndex(p => p[0] === d[0] && p[1] === d[1]);
                    }
                })
                .on("drag", (event) => {
                    if (this.draggingPoint !== null && !envState.isCtrlPressed) {
                        this.environment.voronoiPoints[this.draggingPoint] = [Math.max(0, Math.min(this.environment.width, event.x)), Math.max(0, Math.min(this.environment.height, event.y))];
                        //requestAnimationFrame(render);
                        this.environment.update();
                        renderUtils.renderAll();
                        //computeVoronoi();
                        //render();
                    }
                })
                .on("end", () => {
                    if (this.draggingPoint !== null && !envState.isCtrlPressed) {
                        // socket.emit("updatePoint", {
                        //     index: draggingPoint,
                        //     x: points[draggingPoint][0],
                        //     y: points[draggingPoint][1]
                        // });
                        this.draggingPoint = null;
                    }
                })
            )
            .on("contextmenu", (event, d) => {
                if (!envState.isCtrlPressed) {
                    event.preventDefault();
                    event.stopPropagation(); // Prevent SVG context menu from showing
                    this.selectedPoint = d;
                    [envState.mouseXPos, envState.mouseYPos] = d3.pointer(event, this.svg.node());
                    this.showContextMenu('element', event.pageX, event.pageY);
                }
            });
    }

    drawEditablePolygons() {
        let index = -1;
        const self = this;

        //this.svg.selectAll(".edit-polygon").remove();
        //this.svg.selectAll(".edit-vertex").remove();

        // Draw Voronoi Polygons (Clipped)
        this.svg.selectAll(".edit-polygon")
            .data(this.environment.editPolygonIndices)
            .enter().append("polygon")
            .attr("class", "edit-polygon")
            .attr("points", d => d.map(i => this.environment.editVertices[i].join(",")).join(" "))
            .attr("fill", (d, i) => i === this.environment.selectedPolygonIdx ? "rgba(145,180,227,0.6)" : "rgba(225,225,225,0.7)")
            .attr("stroke", "blue")
            .attr("stroke-width", 2.0)
            .on("mouseover", function (event, d) {
                if (!envState.isCtrlPressed) {

                    index = self.environment.editPolygonIndices.indexOf(d);
                    d3.select(this).attr("fill", "rgba(255,165,0,0.6)"); // Highlight with orange
                    self.tooltip.style("visibility", "visible")
                        .text(`Cell: ${index + 1}`);
                    highlightUtils.highlightBar(index);
                } else {
                    self.tooltip.style("visibility", "hidden");
                }
            })
            .on("mousemove", function (event) {
                if (!envState.isCtrlPressed) {
                    self.tooltip.style("top", `${event.pageY + 10}px`)
                        .style("left", `${event.pageX + 10}px`);
                    highlightUtils.highlightBar(index);
                } else {
                    self.tooltip.style("visibility", "hidden");
                }
            })
            .on("mouseout", function () {
                if (!envState.isCtrlPressed) {
                    self.tooltip.style("visibility", "hidden");
                    if(index !== self.environment.selectedPolygonIdx)
                        d3.select(this).attr("fill", "rgba(225,225,225,0.7)"); // Reset
                    else
                        d3.select(this).attr("fill", "rgba(145,180,227,0.6)"); // Reset
                    highlightUtils.removeHighlightBar(index);
                } else {
                    self.tooltip.style("visibility", "hidden");
                }
            });

        // Draw polygon vertices
        this.svg.selectAll(".edit-vertex")
            .data(this.environment.editVertices)
            .enter().append("circle")
            .attr("class", "edit-vertex")
            .attr("cx", d => d[0])
            .attr("cy", d => d[1])
            .attr("r", 5)
            .attr("fill", "blue")
            .call(d3.drag()
                .on("drag", (event, d) => {
                    d[0] = Math.max(0, Math.min(this.environment.width, event.x));
                    d[1] = Math.max(0, Math.min(this.environment.height, event.y));
                    this.environment.update();
                    renderUtils.renderAll();
                })
            );
    }

    drawLines() {
        this.environment.lines.forEach(points => {
            const polylinePoints = points.map(d => d.join(",")).join(" ");

            // draw direct line if the polyline has more than 2 points
            if (points.length > 2 && envState.showDirectLines) {
                this.svg.append("line")
                    .attr("class", "line")
                    .attr("x1", points[0][0])
                    .attr("y1", points[0][1])
                    .attr("x2", points[points.length - 1][0])
                    .attr("y2", points[points.length - 1][1])
                    .attr("stroke", `rgb(15, 179, 179)`)
                    .style("stroke-dasharray", ("3, 3"))
                    .attr("stroke-width", 2);
            }

            // draw polyline
            this.svg.append("polyline")
                .attr("points", polylinePoints)
                .attr("class", "line")
                .attr("fill", "none")
                .attr("stroke", "cyan")
                .attr("stroke-width", 2);
        });
    }

    drawParticles() {
        this.svg.selectAll(".particle-circle")
            .data(this.environment.particlePositions)
            .enter().append("circle")
            .attr("class", "particle-circle")
            .attr("cx", d => d[0])
            .attr("cy", d => d[1])
            .attr("r", 5)
            .attr("fill", "black");
    }

    drawClippingPolygon() {
        // Draw Clipping Polygon (Boundary)
        this.svg.append("polygon")
            .attr("points", this.environment.clippingPolygon[0].map(p => p.join(",")).join(" "))
            .attr("stroke", "blue")
            .attr("fill", "none")
            .attr("stroke-width", 2);

        // Draw Clipping Polygon Points (Blue)
        this.svg.selectAll(".polygon-point")
            .data(this.environment.clippingPolygon[0])
            .enter().append("circle")
            .attr("class", "clipping-vertex")
            .attr("cx", d => d[0])
            .attr("cy", d => d[1])
            .attr("r", 6)
            .attr("fill", "blue")
            .call(d3.drag()
                .on("start", (_, d) => {
                    if (!envState.isCtrlPressed) {
                        this.draggingPolygonPoint = this.environment.clippingPolygon[0].findIndex(p => p[0] === d[0] && p[1] === d[1]);
                    }
                })
                .on("drag", (event) => {
                    if (this.draggingPolygonPoint !== null && !envState.isCtrlPressed) {
                        this.environment.clippingPolygon[0][this.draggingPolygonPoint] = [Math.max(0, Math.min(this.environment.width, event.x)), Math.max(0, Math.min(this.environment.height, event.y))];
                        //requestAnimationFrame(render);
                        //computeVoronoi();
                        //render();
                        this.environment.update();
                        renderUtils.renderAll();
                    }
                })
                .on("end", () => {
                    if (this.draggingPolygonPoint !== null && !envState.isCtrlPressed) {
                        // socket.emit("updatePolygon", {
                        //     index: draggingPolygonPoint,
                        //     x: clippingPolygon[0][draggingPolygonPoint][0],
                        //     y: clippingPolygon[0][draggingPolygonPoint][1]
                        // });
                        this.draggingPolygonPoint = null;
                    }
                })
            );
    }

    drawGridCell() {
        // Draw grid cells
        this.svg.selectAll(".grid").remove();

        let visibleCells = envState.isCtrlPressed ? this.environment.gridCells : this.environment.gridCells.filter(d => this.environment.selectedCells.has(d.index));

        // first, draw an empty white box before showing grid cells to
        // ensure that grid selection outside of the clipping polygon keeps on working well
        if(envState.isCtrlPressed) this.drawEmptySVG();

        this.svg.selectAll(".grid")
            .data(visibleCells)
            .enter()
            .append("rect")
            .attr("class", "grid")
            .attr("x", d => d.x)
            .attr("y", d => d.y)
            .attr("width", this.environment.cellSize)
            .attr("height", this.environment.cellSize)
            .attr("fill", d => this.environment.selectedCells.has(d.index) ? "orange" : "none")
            .attr("stroke", "lightgrey");
    }

    drawHeatMap() {
        // Color scale based on point density
        const maxDensity = d3.max(this.environment.heatmapGrid); // parseInt(d3.max(heatmapGrid.map(grid => grid.value)));
        const colorScaleHM = envState.activeTab === 'heatmap' ? d3.scaleSequential(d3.interpolateInferno)
            .domain([0, maxDensity]) : d3.scaleSequential(d3.interpolateBlues)
            .domain([0, maxDensity]);

        this.svg.selectAll(".heatmap").remove(); // Remove old heatmap

        const self = this;

        // Draw grid cells
        this.svg.selectAll(".heatmap")
            .data(this.environment.gridCells)
            .enter()
            .append("rect")
            .attr("class", "heatmap")
            .attr("x", d => d.x)
            .attr("y", d => d.y)
            .attr("width", this.environment.cellSize)
            .attr("height", this.environment.cellSize)
            .attr("fill", d => colorScaleHM(this.environment.heatmapGrid[d.index]))
            .attr("stroke", "none")
            .attr("data-index", (d, i) => i)
            .on("mouseover", function (event, d) {
                if (!envState.isCtrlPressed) {

                    const value = self.environment.heatmapGrid[d.index];

                    d3.select(this).attr("stroke", "rgb(255,255,255)"); // outline in white

                    self.tooltip.style("visibility", "visible")
                        .text(`${value}`);

                } else {
                    self.tooltip.style("visibility", "hidden");
                }
            })
            .on("mousemove", function (event) {
                if (!envState.isCtrlPressed) {
                    self.tooltip.style("top", `${event.pageY + 10}px`)
                        .style("left", `${event.pageX + 10}px`);
                } else {
                    self.tooltip.style("visibility", "hidden");
                }
            })
            .on("mouseout", function () {
                if (!envState.isCtrlPressed) {
                    d3.select(this).attr("stroke", "none"); // Reset
                    self.tooltip.style("visibility", "hidden");
                } else {
                    self.tooltip.style("visibility", "hidden");
                }
            });
    }

    drawGraphLineCrossings() {
        this.svg.selectAll(".graphcrossing-point").remove(); // Remove old points

        this.svg.append("g")
            .selectAll(".graphcrossing-point")
            .data(this.environment.crossings)
            .enter()
            .append("circle")
            .attr("class", "graphcrossing-point")
            .attr("cx", d => d[0])
            .attr("cy", d => d[1])
            .attr("r", 3)
            //.attr("fill", "#FD3DB5") // Magenta color  6b486b
            .attr("fill", "#aa6caa") // Magenta color  6b486b
            .attr('opacity', 0.8)
            .attr("stroke", "black")
            .attr("stroke-width", 1);
    }

    drawWeatherCells(x,y,width,height) {
        const imagePath = "data/image/weathercell_small.png";  // relative path to subfolder

        this.svg.append("image")
            .attr("class", "weather")
            .attr("href", imagePath)
            .attr("x", x)
            .attr("y", y)
            .attr("width", width)
            .attr("height", height);
    }

    drawLineCrossings() {
        this.svg.selectAll(".crossing-point").remove(); // Remove old points

        this.svg.selectAll(".crossing-point")
            .data(this.environment.crossings)
            .enter()
            .append("circle")
            .attr("class", "crossing-point")
            .attr("cx", d => d[0])
            .attr("cy", d => d[1])
            .attr("r", 3)
            //.attr("fill", "#FD3DB5") // Magenta color  6b486b
            .attr("fill", "#aa6caa") // Magenta color  6b486b
            .attr('opacity', 0.8)
            .attr("stroke", "black")
            .attr("stroke-width", 1);
    }

    drawDelaunayTriangulation() {
        // Draw Delaunay lines
        const delaunay = d3.Delaunay.from(this.environment.voronoiPoints);

        const triangles = delaunay.triangles;
        const halfedges = delaunay.halfedges;

        const edges = new Set();
        for (let i = 0; i < halfedges.length; i++) {
            const j = halfedges[i];
            if (j > i) {
                edges.add([triangles[i], triangles[j]]);
            }
        }

        // Convert Set to Array
        const links = Array.from(edges);

        // Draw Delaunay lines
        this.svg.append("g")
            .selectAll("delaunay-line")
            .data(links)
            .enter().append("line")
            .attr("class", "delaunay-line")
            .attr("x1", d => this.environment.voronoiPoints[d[0]][0])
            .attr("y1", d => this.environment.voronoiPoints[d[0]][1])
            .attr("x2", d => this.environment.voronoiPoints[d[1]][0])
            .attr("y2", d => this.environment.voronoiPoints[d[1]][1])
            .attr("stroke", "steelblue");
    }

    drawIntersectionPoints() {
        this.svg.selectAll(".intersection")
            .data(this.environment.intersectionPoints)
            .enter().append("circle")
            .attr("class", "intersection")
            .attr("cx", d => d[0])
            .attr("cy", d => d[1])
            .attr("r", d => (d[2] < 25.0 || d[2] > 165.0) ? 5 : 3)
            //.attr("fill", "rgba(0,200,200,1.0)")  // #98abc5
            .attr("fill", d => (d[2] < 25.0 || d[2] > 165.0) ? "red" : "#98abc5")  // #98abc5
            .attr("stroke", "black");
    }

    drawProblematicSegments() {
        this.svg.selectAll(".conflict-line")
            .data(this.environment.problemSegments)
            .enter().append("line")
            .attr("class", "conflict-line")
            .attr("x1", d => d[0][0])
            .attr("y1", d => d[0][1])
            .attr("x2", d => d[1][0])
            .attr("y2", d => d[1][1])
            .attr("stroke", "rgba(255,0,0,0.7)")
            .attr("stroke-width", 2);
    }

    drawProblematicCrossings() {
        this.svg.selectAll(".conflict-crossing-point").remove(); // Remove old points

        this.svg.selectAll(".conflict-crossing-point")
            .data(this.environment.problemLinePolygonCrossings)
            .enter()
            .append("circle")
            .attr("class", "conflict-crossing-point")
            .attr("cx", d => d[0])
            .attr("cy", d => d[1])
            .attr("r", envState.distance_threshold)
            .attr("fill", "none") // Magenta color  6b486b
            .attr("stroke", "#e30945")
            .style("stroke-dasharray", ("3, 3"))
            .attr("stroke-width", 1);
    }

    drawProblematicVertices() {
        this.svg.selectAll(".conflict-vertices").remove(); // Remove old points

        this.svg.selectAll(".conflict-vertices")
            .data(this.environment.problemVertices)
            .enter()
            .append("circle")
            .attr("class", "conflict-vertices")
            .attr("cx", d => d[0])
            .attr("cy", d => d[1])
            .attr("r", envState.vertex_line_threshold)
            .attr("fill", "none") // Magenta color  6b486b
            .attr("stroke", "#e38c09")
            .style("stroke-dasharray", ("3, 3"))
            .attr("stroke-width", 1);
    }

    drawProblematicParticles(problem_particles) {
        const problems = problem_particles.flatMap(row => row);

        this.svg.selectAll(".conflict-particle")
            .data(problems)
            .enter().append("circle")
            .attr("class", "conflict-particle")
            .attr("cx", d => d[0])
            .attr("cy", d => d[1])
            .attr("r", 5)
            .attr("fill", "red");
    }

    drawWindField() {

        // prevent drawing the wind field when no data has been finished loading
        // especially when loading from a file that uses asynchronous fetch()! (see environment class)
        if(!this.environment.wind_loaded) return;

        this.svg.select(".wind-vector").remove();

        const lengthMultiplier = 10;

        let defs = this.svg.select("defs");
        if (defs.empty()) {
            defs = this.svg.append("defs");
        }

        defs.selectAll("marker").remove();

        defs.append("marker")
            .attr("id", "arrow")
            .attr("viewBox", "0 -5 10 10")
            .attr("refX", 8)
            .attr("refY", 0)
            .attr("markerWidth", 4)
            .attr("markerHeight", 4)
            .attr("orient", "auto")
            .append("path")
            .attr("d", "M0,-5L10,0L0,5")
            .attr("fill", "inherit");


        // Color scale for speeds
        const colorScale = d3.scaleLinear()
            .domain([0.2, 1, 2, 3.5])
            .range(["green", "yellow", "orange", "red"]);

        // const colorScale = d3.scaleLinear()
        //     .domain([0, 10])
        //     .range(["#dddddd", "#222222"]);  // light gray to dark gray

        // Draw arrows
        this.svg.selectAll(".wind-vector")
            .data(this.environment.wind_vectors)
            .join("line")
            .attr("class", "wind-vector")
            .attr("x1", d => d.x)
            .attr("y1", d => d.y)
            .attr("x2", d => d.x + (d.vx / d.speed) * lengthMultiplier * d.speed)
            .attr("y2", d => d.y + (d.vy / d.speed) * lengthMultiplier * d.speed)
            .attr("stroke", d => colorScale(d.speed))
            .attr("stroke-width", 1.0)
            .attr("marker-end", "url(#arrow)");
    }

    addPoint() {
        if (envState.mouseXPos === -1 || envState.mouseYPos === -1)
            return;

        this.environment.voronoiPoints.push([envState.mouseXPos, envState.mouseYPos]);
        this.environment.generateRandomSpeeds(this.environment.voronoiPoints.length);
        this.hideContextMenu();
        this.environment.update();
        renderUtils.renderAll();
    }

    removePoint() {
        this.environment.voronoiPoints = this.environment.voronoiPoints.filter(p => p !== this.selectedPoint);
        this.environment.generateRandomSpeeds(this.environment.voronoiPoints.length);
        this.environment.update();
        renderUtils.renderAll();
    }

    clusterPoints() {
        const clustering = kmeansStability(this.environment.crossings, this.environment.voronoiPoints.length);
        // const clustering = kmeansStability(dataset, points.length, true, heatmapGrid);
        this.environment.clusterStability = clustering.stability;
        this.environment.targetPoints = clustering.centroids;
        this.environment.generateRandomSpeeds(this.environment.voronoiPoints.length);
        if(this.environment.targetPoints.length !== this.environment.voronoiPoints.length) {
            this.environment.generateRandomPoints(this.environment.targetPoints.length);
        }
        this.environment.startTime = null;
        requestAnimationFrame((timestamp) => this.transitionPoints(timestamp));
    }

    // function to smoothly transition Voronoi points
    transitionPoints(timestamp) {
        if (!this.environment.startTime) this.environment.startTime = timestamp;
        const elapsed = timestamp - this.environment.startTime;
        const t = Math.min(elapsed / envState.transitionDuration, 1); // Linear normalized time
        const easedT = easeInOutCubic(t); // Apply easing

        this.environment.voronoiPoints = this.environment.voronoiPoints.map((point, index) => {
            let [x, y] = point;

            x += (this.environment.targetPoints[index][0] - x) * easedT;
            y += (this.environment.targetPoints[index][1] - y) * easedT;

            return [x, y];  // Return updated point position
        });

        this.environment.update();
        renderUtils.renderAll();

        if (t < 1) {
            requestAnimationFrame((timestamp) => this.transitionPoints(timestamp));
        }
    }

    highlightVoronoi(index) {
        this.svg.selectAll(".polygon")
            .attr("fill", (d, i) => {
                if (i === index && i !== this.environment.selectedPolygonIdx)
                    return "rgba(255,165,0,0.6)"; // orange for hover
                if (i === this.environment.selectedPolygonIdx)
                    return "rgba(145,180,227,0.6)"; // blue for selected
                return "rgba(225,225,225,0.7)";
            });
    }

    removeHighlightVoronoi(index) {
        this.svg.selectAll(".polygon")
            .attr("fill", (d, i) => {
                return i === this.environment.selectedPolygonIdx
                    ? "rgba(145,180,227,0.6)"
                    : "rgba(225,225,225,0.7)";
            });
    }

    highlightPolygon(index) {
        this.svg.selectAll(".edit-polygon")
            .attr("fill", (d, i) => {
                if (i === index && i !== this.environment.selectedPolygonIdx)
                    return "rgba(255,165,0,0.6)"; // orange for hover
                if (i === this.environment.selectedPolygonIdx)
                    return "rgba(145,180,227,0.6)"; // blue for selected
                return "rgba(225,225,225,0.7)";
            });
    }

    removeHighlightPolygon(index) {
        this.svg.selectAll(".edit-polygon")
            .attr("fill", (d, i) => {
                return i === this.environment.selectedPolygonIdx
                    ? "rgba(145,180,227,0.6)"
                    : "rgba(225,225,225,0.7)";
            });
    }

    getContextMenuItems() {
        return [
            {
                context: 'element',
                tab: 'environment',
                condition: true,
                label: 'remove point',
                action: () => this.removePoint()
            },
            {
                context: 'global',
                tab: 'environment',
                condition: (envState.editMode === 'voronoi'),
                label: 'add point',
                action: () => this.addPoint()
            },
            {
                context: 'global',
                tab: 'environment',
                condition: true,
                label: `mode: ${envState.editMode}`,
                action: () => this.switchEditMode()
            },
            {
                context: 'global',
                tab: 'heatmap',
                condition: true,
                label: 'save as PNG',
                action: () => this.saveAsPNG()
            },
            {
                context: 'element',
                tab: 'graph',
                condition: (envState.editMode === 'voronoi'),
                label: 'remove point',
                action: () => this.removePoint()
            },
            {
                context: 'global',
                tab: 'graph',
                condition: (envState.editMode === 'voronoi'),
                label: 'add point',
                action: () => this.addPoint()
            },
            {
                context: 'global',
                tab: 'graph',
                condition: (envState.editMode === 'voronoi'),
                label: 'cluster',
                action: () => this.clusterPoints()
            }
        ];
    }

    showContextMenu(context, x, y) {
        const menu = this.contextMenu;

        this.hideContextMenu();

        menu.style("left", `${x}px`)
            .style("top", `${y}px`)
            .style("display", "block");

        menu.selectAll("li").remove();

        const items = this.getContextMenuItems();

        menu.selectAll("li")
            .data(items.filter(item => (item.tab === envState.activeTab && item.context === context && item.condition)))
            .enter()
            .append("li")
            .text(d => d.label)
            .on("click", (event, d) => {
                d.action();
                this.hideContextMenu();
            });
    }

    hideContextMenu() {
        this.contextMenu.style("display", "none");
    }

    switchEditMode() {
        envState.editMode = envState.editMode === 'voronoi' ? 'polygon' : 'voronoi';
        if (envState.editMode === 'voronoi') this.environment.update();
        renderUtils.renderAll();
    }

    saveAsSVG() {
        const serializer = new XMLSerializer();
        const svgData = serializer.serializeToString(this.svgCanvas.node());
        const blob = new Blob([svgData], {type: "image/svg+xml;charset=utf-8"});
        const url = URL.createObjectURL(blob);

        const a = document.createElement("a");
        a.href = url;
        a.download = "voronoi_diagram.svg";
        document.body.appendChild(a);
        a.click();
        document.body.removeChild(a);
        URL.revokeObjectURL(url);
    }

    async saveAsPNG() {
        // first, restore zoom
        const t = this.svg.transition();

        t.call(this.zoom.transform,
                d3.zoomIdentity.scale(1.0)
        );

        // await until the zoom has been completed before
        // saving to PNG. Otherwise, the environment might not be fully captured
        await t.end();

        const svgData = new XMLSerializer().serializeToString(this.svg.node());
        const scale = 1.0; // 1.0/cellSize; // each unit is a pixel

        const viewBox = this.svgCanvas.attr("viewBox").split(" ").map(Number);
        const vbWidth = viewBox[2];
        const vbHeight = viewBox[3];

        const canvas = document.createElement("canvas");
        canvas.width = vbWidth * scale;//svg.width.baseVal.value;
        canvas.height = vbHeight * scale;//svg.height.baseVal.value;

        // Use canvg to render the SVG string onto the canvas
        const ctx = canvas.getContext("2d");
        const v = await canvg.Canvg.fromString(ctx, svgData);
        ctx.scale(scale, scale); // scale canvas context
        await v.render();

        // Export canvas as PNG
        const pngLink = document.createElement("a");
        pngLink.download = "heatmap.png";
        pngLink.href = canvas.toDataURL("image/png");
        pngLink.click();
    }

    updateVisibility() {
        d3.selectAll(".crossing-point").style("display", envState.showCrossings ? "block" : "none");
        d3.selectAll(".conflict-crossing-point").style("display", envState.showCrossings ? "block" : "none");
        d3.selectAll(".intersection").style("display", envState.showIntersections ? "block" : "none");
        d3.selectAll(".conflict-vertices").style("display", envState.showIntersections ? "block" : "none");
        d3.selectAll(".voronoi-point").style("display", envState.showVoronoiPoints ? "block" : "none");
        d3.selectAll(".line").style("display", envState.showLines ? "block" : "none");
        d3.selectAll(".conflict-line").style("display", envState.showLines ? "block" : "none");
        d3.selectAll(".delaunay-line").style("display", envState.showDelaunay ? "block" : "none");
        d3.selectAll(".particle-circle").style("display", envState.showParticles ? "block" : "none");
        d3.selectAll(".polygon").style("display", envState.showPolygons ? "block" : "none");
        d3.selectAll(".voronoi-vertex").style("display", envState.showPolygons ? "block" : "none");
        d3.selectAll(".weather").style("display", envState.showWeather ? "block" : "none");
        d3.selectAll(".wind-vector").style("display", envState.showWindField ? "block" : "none");
    }

    render() {
        // first, remove all contents from the D3 canvas
        this.svg.selectAll("*").remove();

        // note: the order in which things are drawn determine what
        // elements are drawn op top of each other
        if (envState.activeTab === "graph")
        {
            this.drawEmptySVG(); // needed to support "global" contextmenu when right clicking
            this.drawGraphLineCrossings();
            this.drawDelaunayGraph();
        }
        else if (envState.activeTab === "heatmap")
        {
            this.drawHeatMap();
        }
        else if (envState.activeTab === "environment")
        {
            this.drawWeatherCells(400, 300, 200, 200);

            this.drawLines();
            this.drawLineCrossings();
            this.drawParticles();

            this.drawDelaunayTriangulation();

            // update clipped voronoi
            envState.editMode === 'voronoi' ? this.drawVoronoi() : this.drawEditablePolygons();

            this.drawIntersectionPoints();
            this.drawProblematicSegments();
            this.drawProblematicCrossings();
            this.drawProblematicVertices();
            this.drawProblematicParticles(this.environment.calculateParticleSeparationProblems(this.environment.particlePositions));

            if(envState.editMode === 'voronoi') this.drawClippingPolygon();

            // draw the grid cells last (when active is CTRL key is pressed)
            this.drawGridCell();
            
            this.drawWindField();

        }

        this.updateVisibility();
    }

} // close class