// Code from https://medium.com/geekculture/implementing-k-means-clustering-from-scratch-in-javascript-13d71fbcb31e
//
// Uses a more sophisticated initialization method called Naive Sharding
// ( https://www.kdnuggets.com/2017/03/naive-sharding-centroid-initialization-method.html )
//

const {color} = require("d3");
const MAX_ITERATIONS = 10000;

function randomBetween(min, max) {
    return Math.floor(
        Math.random() * (max - min) + min
    );
}

function calcMeanCentroid(dataSet, start, end) {
    const features = dataSet[0].length;
    const n = end - start;
    let mean = [];
    for (let i = 0; i < features; i++) {
        mean.push(0);
    }
    for (let i = start; i < end; i++) {
        for (let j = 0; j < features; j++) {
            mean[j] = mean[j] + dataSet[i][j] / n;
        }
    }
    return mean;
}

function getRandomCentroidsNaiveSharding(dataset, k) {
    // implementation of a variation of naive sharding centroid initialization method
    // (not using sums or sorting, just dividing into k shards and calc mean)
    // https://www.kdnuggets.com/2017/03/naive-sharding-centroid-initialization-method.html
    const numSamples = dataset.length;
    // Divide dataset into k shards:
    const step = Math.floor(numSamples / k);
    const centroids = [];
    for (let i = 0; i < k; i++) {
        const start = step * i;
        let end = step * (i + 1);
        if (i + 1 === k) {
            end = numSamples;
        }
        centroids.push(calcMeanCentroid(dataset, start, end));
    }
    return centroids;
}

function getRandomCentroids(dataset, k) {
    // selects random points as centroids from the dataset
    const numSamples = dataset.length;
    const centroidsIndex = [];
    let index;
    while (centroidsIndex.length < k) {
        index = randomBetween(0, numSamples);
        if (centroidsIndex.indexOf(index) === -1) {
            centroidsIndex.push(index);
        }
    }
    const centroids = [];
    for (let i = 0; i < centroidsIndex.length; i++) {
        const centroid = [...dataset[centroidsIndex[i]]];
        centroids.push(centroid);
    }
    return centroids;
}

function compareCentroids(a, b) {
    for (let i = 0; i < a.length; i++) {
        if (a[i] !== b[i]) {
            return false;
        }
    }
    return true;
}

function shouldStop(oldCentroids, centroids, iterations) {
    if (iterations > MAX_ITERATIONS) {
        return true;
    }
    if (!oldCentroids || !oldCentroids.length) {
        return false;
    }
    let sameCount = true;
    for (let i = 0; i < centroids.length; i++) {
        if (!compareCentroids(centroids[i], oldCentroids[i])) {
            sameCount = false;
        }
    }
    return sameCount;
}

// Calculate Squared Euclidean Distance
function getDistanceSQ(a, b) {
    const diffs = [];
    for (let i = 0; i < a.length; i++) {
        diffs.push(a[i] - b[i]);
    }
    return diffs.reduce((r, e) => (r + (e * e)), 0);
}

// Returns a label for each piece of data in the dataset.
function getLabels(dataSet, centroids) {
    // prep data structure:
    const labels = {};
    for (let c = 0; c < centroids.length; c++) {
        labels[c] = {
            points: [],
            centroid: centroids[c],
        };
    }
    // For each element in the dataset, choose the closest centroid.
    // Make that centroid the element's label.
    for (let i = 0; i < dataSet.length; i++) {
        const a = dataSet[i];
        let closestCentroid, closestCentroidIndex, prevDistance;
        for (let j = 0; j < centroids.length; j++) {
            let centroid = centroids[j];
            if (j === 0) {
                closestCentroid = centroid;
                closestCentroidIndex = j;
                prevDistance = getDistanceSQ(a, closestCentroid);
            } else {
                // get distance:
                const distance = getDistanceSQ(a, centroid);
                if (distance < prevDistance) {
                    prevDistance = distance;
                    closestCentroid = centroid;
                    closestCentroidIndex = j;
                }
            }
        }
        // add point to centroid labels:
        labels[closestCentroidIndex].points.push(a);
    }
    return labels;
}

function getPointsMean(pointList) {
    const totalPoints = pointList.length;
    const means = [];
    for (let j = 0; j < pointList[0].length; j++) {
        means.push(0);
    }
    for (let i = 0; i < pointList.length; i++) {
        const point = pointList[i];
        for (let j = 0; j < point.length; j++) {
            const val = point[j];
            means[j] = means[j] + val / totalPoints;
        }
    }
    return means;
}

function recalculateCentroids(dataSet, labels, k) {
    // Each centroid is the geometric mean of the points that
    // have that centroid's label. Important: If a centroid is empty (no points have
    // that centroid's label) you should randomly re-initialize it.
    let newCentroid;
    const newCentroidList = [];
    for (const k in labels) {
        const centroidGroup = labels[k];
        if (centroidGroup.points.length > 0) {
            // find mean:
            newCentroid = getPointsMean(centroidGroup.points);
        } else {
            // get new random centroid
            newCentroid = getRandomCentroids(dataSet, 1)[0];
        }
        newCentroidList.push(newCentroid);
    }
    return newCentroidList;
}

function createWeightedDataset(dataset, weights, scale) {

    if(weights.length === 0) {return dataset;}

    let weightedDataset = [];

    //console.log(weights);

    if(weights.length > 0 && dataset.length === weights.length) {

        //console.log('here');
        for(let i = 0; i < weights.length; i++) {
            const amount = Math.ceil(weights[i]/scale);
            for (let j = 0; j < amount; j++) {
                weightedDataset.push(dataset[i]);
            }
        }
    }
    return weightedDataset;
}

function kmeans(dataset, k, useNaiveSharding = true, weights = [], scale = 10) {

    dataset = createWeightedDataset(dataset,weights,scale);

    //console.log(dataset);

    if (dataset.length && dataset[0].length && dataset.length > k) {
        // Initialize book keeping variables
        let iterations = 0;
        let oldCentroids, labels, centroids = [];

        // Initialize centroids randomly
        if (useNaiveSharding) {
            centroids = getRandomCentroidsNaiveSharding(dataset, k);
        } else {
            centroids = getRandomCentroids(dataset, k);
        }

        // Run the main k-means algorithm
        while (!shouldStop(oldCentroids, centroids, iterations)) {
            // Save old centroids for convergence test.
            oldCentroids = [...centroids];
            iterations++;

            // Assign labels to each datapoint based on centroids
            labels = getLabels(dataset, centroids);
            centroids = recalculateCentroids(dataset, labels, k);
        }

        const clusters = [];
        for (let i = 0; i < k; i++) {
            clusters.push(labels[i]);
        }

        // generate an array with labels for the points in the dataset
        // this is handy for calculating the ARI score representing
        // the cluster stability over several kmeans cycles
        const lab = [];

        clusters.forEach((cluster, index) => {
            lab.push(...Array(cluster.points.length).fill(index));
        });

        const results = {
            labels: lab,
            clusters: clusters,
            centroids: centroids,
            iterations: iterations,
            converged: iterations <= MAX_ITERATIONS,
        };
        return results;
    } else {
        throw new Error('Invalid dataset');
    }
}

/**
 * Compute Adjusted Rand Index between two clusterings
 * @param {Array<number>} labels1
 * @param {Array<number>} labels2
 * @returns {number} ARI score
 */
function adjustedRandIndex(labels1, labels2) {
    const n = labels1.length;

    const contingency = {};
    const aCounts = {};
    const bCounts = {};

    for (let i = 0; i < n; i++) {
        const a = labels1[i];
        const b = labels2[i];

        contingency[a] = contingency[a] || {};
        contingency[a][b] = (contingency[a][b] || 0) + 1;

        aCounts[a] = (aCounts[a] || 0) + 1;
        bCounts[b] = (bCounts[b] || 0) + 1;
    }

    const comb2 = x => (x * (x - 1)) / 2;

    let index = 0;
    for (const a in contingency) {
        for (const b in contingency[a]) {
            index += comb2(contingency[a][b]);
        }
    }

    const aSum = Object.values(aCounts).reduce((sum, val) => sum + comb2(val), 0);
    const bSum = Object.values(bCounts).reduce((sum, val) => sum + comb2(val), 0);
    const totalComb = comb2(n);

    const expectedIndex = (aSum * bSum) / totalComb;
    const maxIndex = 0.5 * (aSum + bSum);

    return (index - expectedIndex) / (maxIndex - expectedIndex);
}

/**
 * Evaluate the stability of KMeans clustering using ARI
 * @param {Array<Array<number>>} data - 2D data points
 * @param {number} k - Number of clusters
 * @param {number} runs - Number of KMeans runs
 * @returns {number}  Clusters with ARI stability score: -1 (worst) to 1 (perfect agreement); random clustering gives ARI near 0.
 */
function kmeansStability(data, k, useNaiveSharding = true, weights = [], scale = 10, runs = 10) {
    const clusterings = [];
    let result = [];

    for (let i = 0; i < runs; i++) {
        result =  kmeans(data, k, useNaiveSharding, weights, scale);
        clusterings.push(result.labels);
    }

    // Compare all pairs of clusterings using ARI
    let totalARI = 0;
    let comparisons = 0;
    for (let i = 0; i < clusterings.length; i++) {
        for (let j = i + 1; j < clusterings.length; j++) {
            totalARI += adjustedRandIndex(clusterings[i], clusterings[j]);
            comparisons++;
        }
    }

    return {
        stability: totalARI / comparisons,
        clusters: result.clusters,
        centroids: result.centroids,
        iterations: result.iterations,
        converged: result.iterations <= MAX_ITERATIONS,
    };
}



module.exports = { kmeans, kmeansStability};
