import {Environment} from "../environment/environment.js";
import { envState } from "../settings";
import { highlightUtils } from '../newmain.js';
import { renderUtils } from '../newmain.js';
import '../styles.css';
import * as d3 from "d3";
import {isPointInsidePolygon} from "../utility/geometry";

export class Chart {
    constructor(environment) {
        this.environment = environment;
        this.chartWidth = 600;
        this.chartHeight = 300;
        this.showLineChart = false;
        this.chartSvg = undefined;
        this.barChartSvg = undefined;
        this.lineChartSvg = undefined;
        this.legend = undefined;
        this.categories = undefined;
        this.colorScale = undefined;
        this.variance = -1.0;
        this.peak = -1.0;
        this.std = -1.0;
        this.avg = -1.0;
        this.lineData = [];

        highlightUtils.setInstance("chart", this);
        renderUtils.setInstance("chart", this);
    }

    init() {
        this.chartSvg = d3.select("#barchart-container")
            .append("svg")
            .attr("width", this.chartWidth)
            .attr("height", this.chartHeight);

        this.categories = ['nLIntersections', 'nLCrossingPoints', 'nParticles', 'nConflicts'];  // Three fixed categories
        this.colorScale = d3.scaleOrdinal()
            .domain(this.categories)
            .range(['#98abc5', '#aa6caa', '#ff8c00','#e30945']);

        // Add Legend under the chart, horizontally
        this.legend = d3.select("#legend");

        this.categories.forEach(key => {
            const legendItem = this.legend.append("div").attr("class", "legend-item");

            legendItem.append("div")
                .attr("class", "legend-rect")
                .style("background-color", this.colorScale(key));

            legendItem.append("span").text(key.charAt(0).toUpperCase() + key.slice(1));
        });

        this.barChartSvg = this.chartSvg.append("g").attr("class", "bars");
        this.lineChartSvg = this.chartSvg.append("g").attr("class", "areas").style("display", "none");
    }

    initEventHandlers() {
        this.chartSvg.on("click", () => {
            this.barChartSvg.style("display", "block");
            this.lineChartSvg.style("display", "none");
            this.showLineChart = false;
            this.environment.selectedPolygonIdx = -1;
            this.environment.selectedPolygon = [];
            //console.log("chart clicked");
        });
    }

    updateLineChart() {
        if(!this.showLineChart) return;

        this.barChartSvg.style("display", "none");
        this.lineChartSvg.style("display", "block");

        this.lineChartSvg.selectAll("*").remove();  // Clear previous chart rendering

        this.lineData = this.environment.getTimeLineData();

        // Set up scales
        const xScale = d3.scalePoint()
            .domain(this.lineData.map(d => d.time))
            .range([50, this.chartWidth - 10])
            .padding(0.3);

        const yScale = d3.scaleLinear()
            //.domain([0, d3.max(data, d => d.A + d.B + d.Cross + d.Area)])
            .domain([0, (this.environment.lines.length + this.environment.particles.length + this.environment.crossings.length + Math.ceil(this.environment.numLinePolygonIntersections/this.environment.voronoiPoints.length))])
            .nice()
            .range([this.chartHeight - 50, 10]);


        const stack = d3.stack().keys(this.categories)(this.lineData);

        // X Axis
        this.lineChartSvg.append("g")
            .attr("transform", `translate(0, ${this.chartHeight - 50})`)
            .call(
                d3.axisBottom(xScale)
                    .tickValues(d3.range(0, 240, 10)) // <-- every 10 units
                    .tickSizeOuter(0)                 // <-- no outer ticks
            )
            .selectAll("text")
            .style("display", "none");  // hides them

        // Y Axis
        this.lineChartSvg.append("g")
            .attr("transform", "translate(50, 0)")
            .call(d3.axisLeft(yScale));

        this.lineChartSvg.append("g")
            .selectAll("path")
            .data(stack)
            .join("path")
            .attr("class", "area")
            .attr("d", d3.area()
                .x(d => xScale(d.data.time))
                .y0(d => yScale(d[0]))
                .y1(d => yScale(d[1])))
            .attr("fill", d => this.colorScale(d.key));

        // Add a vertical time line to the chart
        this.lineChartSvg.append("line")
            .attr("x1", xScale(this.environment.timeValue))
            .attr("y1", 0)
            .attr("x2", xScale(this.environment.timeValue))
            .attr("y2", this.chartHeight)
            .attr("stroke", "black")
            .attr("stroke-dasharray", "2")
            .attr("stroke-width", 2);

        // Add a max line to the chart
        this.lineChartSvg.append("line")
            .attr("x1", 0)
            .attr("x2", this.chartWidth)
            .attr("y1", yScale(envState.complexity_limit))
            .attr("y2", yScale(envState.complexity_limit))
            .attr("stroke", "red")
            .attr("stroke-dasharray", "4")
            .attr("stroke-width", 2);
    }

    updateBarChart() {
        if(this.showLineChart) return;

        this.barChartSvg.selectAll("*").remove();  // Clear previous bars

        const self = this;

        //const area_clipping = Math.abs(d3.polygonArea(clippingPolygon[0]));
        //console.log('Clipping Polygon Area: : ' + area_clipping);

        const polys = (envState.editMode === 'voronoi' ? this.environment.polygons : this.environment.editPolygons);

        // Generate data with calculated Voronoi areas
        const data = polys.map((polygon, i) => {
            //const area =  Math.abs(d3.polygonArea(polygon));  // Calculate area
            const num_particles = this.environment.particlePositions.filter(pt => isPointInsidePolygon(pt, polygon)).length;
            const numCrossings = this.environment.crossings.filter(pt => isPointInsidePolygon(pt, polygon)).length;
            const numLineCrossings = this.environment.countLineIntersections(polygon);
            const conflicts = this.environment.countParticleSeparationProblems(this.environment.particlePositions, polygon);

            //console.log('Area: : ' + area);
            //console.log('---');
            return {
                category: `Cell ${i + 1}`,
                //A: 0, //Math.floor(Math.random() * 50 + 10),
                nLIntersections: numLineCrossings, //Math.floor(Math.random() * 50 + 10),
                nLCrossingPoints: numCrossings, //Math.floor(Math.random() * 50 + 10),
                nParticles: num_particles, //(area / area_clipping) * 100  // Normalize area for visualization
                nConflicts: conflicts
            };
        });

        // Set up scales
        const xScale = d3.scaleBand()
            .domain(data.map(d => d.category))
            .range([50, this.chartWidth - 10])
            .padding(0.3);

        const yScale = d3.scaleLinear()
            //.domain([0, d3.max(data, d => d.A + d.B + d.Cross + d.Area)])
            .domain([0, (this.environment.lines.length + this.environment.particles.length + this.environment.crossings.length + Math.ceil(this.environment.numLinePolygonIntersections/this.environment.voronoiPoints.length))])
            .nice()
            .range([this.chartHeight - 50, 10]);

        // Stack data
        const stack = d3.stack()
            .keys(this.categories)
            (data);

        // X Axis
        this.barChartSvg.append("g")
            .attr("transform", `translate(0, ${this.chartHeight - 50})`)
            .call(d3.axisBottom(xScale).tickSizeOuter(0))
            .selectAll("text")
            .attr("transform", "rotate(-45)")
            .style("text-anchor", "end");

        // Y Axis
        this.barChartSvg.append("g")
            .attr("transform", "translate(50, 0)")
            .call(d3.axisLeft(yScale));

        // Draw stacked bars
        this.barChartSvg.append("g")
            .selectAll("g")
            .data(stack)
            .enter()
            .append("g")
            .attr("fill", d => this.colorScale(d.key))
            .selectAll("rect")
            .data(d => d)
            .enter()
            .append("rect")
            .attr("x", d => xScale(d.data.category))
            .attr("y", d => yScale(d[1]))
            .attr("height", d => yScale(d[0]) - yScale(d[1]))
            .attr("width", xScale.bandwidth())
            .attr("data-index", (d, i) => i)
            .on("mouseover", function(event, d) {
                const index = d3.select(this).attr("data-index"); // Get the index
                self.highlightBar(parseInt(index));
                envState.editMode === 'voronoi' ? highlightUtils.highlightVoronoi(parseInt(index)): highlightUtils.highlightPolygon(parseInt(index));
                //console.log("Hovered Bar Index:", index); // Print the index
            })
            .on("mouseout", function() {
                //console.log("Mouse left bar");
                //if(self.environment.selectedPolygonIdx !== -1) {
                const index = d3.select(this).attr("data-index");
                self.removeHighlightBar(parseInt(index));
                envState.editMode === 'voronoi' ? highlightUtils.removeHighlightVoronoi(parseInt(index)) : highlightUtils.removeHighlightPolygon(parseInt(index));
                //} // Get the index
            })
            .on("click", function(event, d) {
                const index = d3.select(this).attr("data-index"); // Get the index
                event.stopPropagation();
                //showLineChart(d.data.category);
                //console.log("clicked on bar: " + index);
                //console.log("polygon: " + polygons[index]);
                self.showLineChart = true;
                self.environment.selectedPolygonIdx = parseInt(index);
                self.lineData = self.environment.getTimeLineData();
                envState.editMode === 'voronoi' ? highlightUtils.highlightVoronoi(parseInt(index)): highlightUtils.highlightPolygon(parseInt(index));
                //selectedPolygon = polygons[index];
                //updateLineChart();
                self.render();
            });

        // Display variance below the bar chart
        this.variance = this.calculateVariance(data);
        this.peak = this.calculatePeak(data);
        this.std = this.calculateStandardDeviation(data);
        this.avg = this.calculateAverage(data);

        // Add a mean line to the chart
        this.barChartSvg.append("line")
            .attr("x1", 0)
            .attr("x2", this.chartWidth)
            .attr("y1", yScale(this.avg))
            .attr("y2", yScale(this.avg))
            .attr("stroke", "blue")
            .attr("stroke-dasharray", "4")
            .attr("stroke-width", 2);

        // Add a max line to the chart
        this.barChartSvg.append("line")
            .attr("x1", 0)
            .attr("x2", this.chartWidth)
            .attr("y1", yScale(envState.complexity_limit))
            .attr("y2", yScale(envState.complexity_limit))
            .attr("stroke", "red")
            .attr("stroke-dasharray", "4")
            .attr("stroke-width", 2);
    }

    calculateVariance(data) {
        const values = data.map(d =>
            d3.sum(this.categories, key => d[key])
        );
        const meanBars = d3.mean(values);
        return d3.mean(values.map(total =>
            Math.pow(total - meanBars, 2)
        ));
    }

    calculatePeak(data) {
        return d3.max(data, d => d.nLIntersections + d.nLCrossingPoints + d.nParticles + d.nConflicts);
    }

    calculateStandardDeviation(data) {
        return Math.sqrt(this.calculateVariance(data));
    }

    calculateAverage(data) {
        return d3.mean(data, d => d.nLIntersections + d.nLCrossingPoints + d.nParticles + d.nConflicts);
    }

    highlightBar(index) {
        this.barChartSvg.selectAll("text")
            //.attr("stroke", (d, i) => i === index ? "black" : "none") // Highlight with black stroke
            //.attr("stroke-width", (d, i) => i === index ? 2 : 0)
            .style("font-size", (d, i) => i === index ? 14 : 10)
            .style("font-weight", (d, i) => i === index ? "bold" : "normal");
    }

    removeHighlightBar(index) {
        this.barChartSvg.selectAll("text")
            //.attr("stroke", (d, i) => i === index ? "black" : "none") // Highlight with black stroke
            //.attr("stroke-width", (d, i) => i === index ? 2 : 0)
            .style("font-size", 10)
            .style("font-weight",  "normal");
    }

    render() {
        this.updateBarChart();
        this.updateLineChart();

        // Display variance in the input box
        //document.getElementById("varianceBox").value = `Variance: ${variance.toFixed(2)}`;
        document.getElementById("stdBox").value = `Standard deviation: ${this.std.toFixed(2)}`;
        document.getElementById("peakBox").value = `Peak value: ${this.peak.toFixed(2)}`;
        document.getElementById("meanBox").value = `Mean value: ${this.avg.toFixed(2)}`;
        document.getElementById("stabilityBox").value = `Stability: ${this.environment.clusterStability.toFixed(4)}`;
    }

} // close class