import {Typewriter} from "./Typewriter.js";
import '../styles.css';
import {envState} from "../settings";

export class OzAssistant {
    constructor(conditionFn) {
        this.typewriter = new Typewriter("oztext-box", 10);
        this.conditionFn = conditionFn;
        this.schedule = undefined;
        this.commandRegistry = undefined;
        this.previewRegistry = undefined;

        // Execution state
        this.timers = [];
        this.intervals = [];
        this.paused = false;
        this.stopped = false;

        this.scheduledItems = []; // Tracks pending items to reschedule after resume

        this.confirmationStats = {
            approved: 0,
            denied: 0
        };

        this.confirmationLog = [];

        this.options = undefined;

        this.scriptStartTime = null;

        this.init();
    }

    init() {
        this.commandRegistry = {
            addPoint: ([x, y]) => {
                // this.environment.voronoiPoints
                // console.log("trying to a point to the environment at: ", x, y);
            }
        };

        this.previewRegistry = {
            addPoint: ([x, y]) => {
                // this.environment.voronoiPoints
                // console.log("trying to a point to the environment at: ", x, y);
            }
        };
    }

    async load(jsonUrl) {
        this.reset();

        try {
            const response = await fetch(jsonUrl);
            if (!response.ok) throw new Error("Failed to fetch messages JSON");

            const data = await response.json();

            if (Array.isArray(data)) {
                // fallback: treat raw array as items
                this.schedule = data;
                this.options = { sequential: false }; // default
            } else {
                this.schedule = Array.isArray(data.items) ? data.items : [];
                this.options = { sequential: false, ...data.options }; // merge with defaults
            }

        } catch (error) {
            console.error("Error loading schedule:", error);
            this.typewriter.typeMessage("Failed to load messages.");
        }
    }

    start() {

        this.scriptStartTime = Date.now(); // 🔥 Set start time once

        if(this.schedule && !this.options.sequential) {
            this.schedule.forEach(item => {
                this.scheduleItem(item, item.time);

                if (item.repeat === "infinite" && item.interval) {
                    const infiniteTimer = setTimeout(() => {
                        this.repeatIndefinitely(item);
                    }, item.time + item.interval);
                    this.timers.push(infiniteTimer);
                } else if (typeof item.repeat === "number" && item.interval) {
                    for (let i = 1; i <= item.repeat; i++) {
                        this.scheduleItem(item, item.time + i * item.interval);
                    }
                }
            });
        }
        else if(this.schedule && this.options.sequential) {
            console.log("run in sequence");
            this.runSequentialScript(this.schedule);
        }
    }

    reset() {
        // Clear all timeouts/intervals
        this.timers.forEach(clearTimeout);
        this.intervals.forEach(clearInterval);
        this.timers = [];
        this.intervals = [];
        this.scheduledItems = [];
        this.paused = false;
        this.stopped = false;
        this.confirmationLog = [];
        this.confirmationStats = {
            approved: 0,
            denied: 0
        };
    }

    stop() {
        this.reset();        // Cancel timers/intervals
        this.stopped = true; // Block new execution
    }

    pause() {
        if (this.paused) return;
        this.paused = true;

        // Cancel scheduled timers
        this.scheduledItems.forEach(sched => {
            clearTimeout(sched.timeoutId);
            const elapsed = Date.now() - sched.startTime;
            sched.remaining = Math.max(0, sched.delay - elapsed);
        });
    }

    resume() {
        if (!this.paused) return;
        this.paused = false;

        this.scheduledItems.forEach(sched => {
            if (this.stopped) return;

            const newTimeoutId = setTimeout(() => {
                if (this.stopped) return;

                if ('message' in sched.item) {
                    this.waitAndType(sched.item.message);
                } else if ('command' in sched.item) {
                    this.waitAndExecute(sched.item);
                }
            }, sched.remaining);

            this.timers.push(newTimeoutId);
            sched.timeoutId = newTimeoutId;
            sched.startTime = Date.now();
            sched.delay = sched.remaining;
            delete sched.remaining;
        });
    }

    sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    async runSequentialScript(items) {
        for (const item of items) {
            if (this.stopped) break;

            if (this.paused) {
                await new Promise(resolve => {
                    const checkResume = setInterval(() => {
                        if (!this.paused && !this.stopped) {
                            clearInterval(checkResume);
                            resolve();
                        }
                    }, 200);
                });
            }

            if ('message' in item) {
                await this.waitAndType(item.message);
            } else if ('command' in item) {
                await this.waitAndExecute(item);
            }

            // Optional delay between items
            if (this.options.stepDelay) {
                await this.sleep(this.options.stepDelay);
            }
        }
    }

    scheduleItem(item, delay) {
        const startTime = Date.now();

        const timeoutId = setTimeout(() => {
            if (this.stopped) return;

            if ('message' in item) {
                this.waitAndType(item.message);
            } else if ('command' in item) {
                this.waitAndExecute(item);
            }
        }, delay);
        this.timers.push(timeoutId);

        // Store metadata for pause/resume
        this.scheduledItems.push({
            item,
            startTime,
            delay,
            timeoutId,
        });
    }

    repeatIndefinitely(item) {
        const handler = () => {
            if (this.paused || this.stopped || !this.conditionFn()) return;

            if ('message' in item) {
                this.typewriter.typeMessage(item.message);
            } else if ('command' in item) {
                const fn = this.commandRegistry[item.command];
                if (typeof fn === "function") {
                    fn((item));
                }
            }
        };

        const intervalId = setInterval(handler, item.interval);
        this.intervals.push(intervalId);

        if (typeof item.maxRuntime === "number") {
            const maxTimer = setTimeout(() => {
                clearInterval(intervalId);
                console.log(`[OzAssistant] Stopped repeating after ${item.maxRuntime}ms`);
            }, item.maxRuntime);
            this.timers.push(maxTimer);
        }
    }

    waitAndType(message) {
        envState.warningTab = ['ai-container'];
        window.GlobalEventBus.dispatchEvent(new CustomEvent('sync-tab-state', {
            detail: envState
        }));

        return new Promise((resolve) => {
            const interval = setInterval(() => {
                if (this.paused || this.stopped) return;

                if (this.conditionFn()) {
                    clearInterval(interval);
                    this.typewriter.typeMessage(message, resolve);  // resolve on complete
                }
            }, 200);

            this.intervals.push(interval);
        });
    }

    waitAndExecute(item) {
        envState.warningTab = ['ai-container'];
        window.GlobalEventBus.dispatchEvent(new CustomEvent('sync-tab-state', {
            detail: envState
        }));

        return new Promise((resolve) => {
            const {
                command: commandName,
                args = [],
                requiresConfirmation = true,
                explanation,
                previewable = false,
                fromUser = false
            } = item;

            const interval = setInterval(async () => {
                if (this.paused || this.stopped) return;

                if (this.conditionFn()) {
                    clearInterval(interval);

                    const triggeredAt = Date.now(); // Time when command becomes eligible
                    const commandFn = this.commandRegistry[commandName];
                    if (typeof commandFn !== "function") {
                        console.warn(`[OzAssistant] Unknown command "${commandName}".`);
                        resolve();
                        return;
                    }

                    let approved = true;
                    let confirmedAt = null;
                    let previewedAt = null;
                    let postPreviewDelay = null;

                    const proceedToExecute = async () => {
                        if (requiresConfirmation !== false) {
                            const result = await this.confirmExecution(commandName, args, previewable);
                            approved = result.approved;
                            confirmedAt = result.confirmedAt;
                            previewedAt = result.previewedAt;
                            postPreviewDelay = result.postPreviewDelay;
                        } else {
                            confirmedAt = Date.now(); // No dialog, still log timestamp
                        }

                        // Track log
                        this.confirmationLog.push({
                            command: commandName,
                            args,
                            approved,
                            triggeredAt,
                            confirmedAt,
                            delay: confirmedAt - triggeredAt,
                            previewedAt,
                            postPreviewDelay,
                            triggeredSinceStart: triggeredAt - this.scriptStartTime,
                            confirmedSinceStart: confirmedAt - this.scriptStartTime,
                            previewedSinceStart: previewedAt ? previewedAt - this.scriptStartTime : null,
                            source: fromUser ? "user" : "script"
                        });

                        if (approved) {
                            commandFn(args);
                        } else {
                            console.log(`[OzAssistant] Command "${commandName}" was blocked by user.`);
                        }

                        //console.table(this.getConfirmationStats().entries);
                        console.table(this.getConfirmationStats().entries.map(entry => ({
                            command: entry.command,
                            triggeredMs: entry.triggeredSinceStart,
                            confirmedMs: entry.confirmedSinceStart,
                            delay: entry.delay,
                            previewedMs: entry.previewedSinceStart,
                            postPreviewDelay: entry.postPreviewDelay,
                            approved: entry.approved
                        })));

                        resolve(); // ✅ Resolve after everything is done
                    };

                    if (explanation) {
                        await this.waitAndType(explanation);
                        await this.sleep(300);
                        await proceedToExecute();
                        this.typewriter.clear();
                    } else {
                        await proceedToExecute();
                    }
                }
            }, 200);

            this.intervals.push(interval);
        });
    }

    async confirmExecution(commandName, args, previewable = false) {
        const dialog = document.getElementById("oz-confirm-dialog");
        const message = document.getElementById("oz-confirm-message");
        const yesBtn = document.getElementById("oz-confirm-yes");
        const noBtn = document.getElementById("oz-confirm-no");
        const previewBtn = document.getElementById("oz-confirm-preview");

        if (!dialog || !message || !yesBtn || !noBtn || !previewBtn) {
            console.warn("Confirmation dialog elements not found.");
            return false;
        }

        let previewedAt = null;

        return new Promise((resolve) => {
            message.textContent = `Allow OzAssistant to execute command: ${commandName}(${args.join(", ")})?`;

            dialog.classList.remove("fade-out");
            dialog.classList.add("fade-in");
            dialog.style.display = "block";

            // Force reflow for animation restart
            void dialog.offsetWidth;

            dialog.classList.add("active");

            // Remove fade-in class after animation
            setTimeout(() => {
                dialog.classList.remove("fade-in");
            }, 300);

            // dialog.style.display = "block";
            // // Prepare dialog fade-in
            // dialog.classList.remove("fade-out");
            // dialog.classList.add("fade-in");
            //
            // // Force reflow for animation restart
            // void dialog.offsetWidth;
            //
            // dialog.classList.add("active");
            //
            // // Remove fade-in class after animation
            // setTimeout(() => {
            //     dialog.classList.remove("fade-in");
            // }, 300);

            //dialog.classList.add("active");
            previewBtn.style.display = previewable ? "inline-block" : "none";

            const cleanup = () => {

                dialog.classList.remove("active");
                dialog.classList.add("fade-out");

                // Remove fade-out after animation, reset opacity
                setTimeout(() => {
                    dialog.classList.remove("fade-out");
                }, 300); // Match the CSS transition duration (300ms)

                yesBtn.removeEventListener("click", onYes);
                noBtn.removeEventListener("click", onNo);
                previewBtn.removeEventListener("click", onPreview);
            };

            const onYes = () => {
                const confirmedAt = Date.now();
                cleanup();
                resolve({
                    approved: true,
                    confirmedAt,
                    previewedAt,
                    postPreviewDelay: previewedAt ? confirmedAt - previewedAt : null
                });
            };

            const onNo = () => {
                const confirmedAt = Date.now();
                cleanup();
                resolve({
                    approved: false,
                    confirmedAt,
                    previewedAt,
                    postPreviewDelay: previewedAt ? confirmedAt - previewedAt : null
                });
            };

            const onPreview = () => {
                previewedAt = Date.now();
                if (typeof this.previewRegistry[commandName] === "function") {
                    this.previewRegistry[commandName](args);
                } else {
                    console.warn(`[OzAssistant] No preview function registered for "${commandName}"`);
                }
            };

            yesBtn.addEventListener("click", onYes);
            noBtn.addEventListener("click", onNo);
            previewBtn.addEventListener("click", onPreview);
        });
    }

    getConfirmationStats() {
        return {
            totalApproved: this.confirmationLog.filter(e => e.approved).length,
            totalDenied: this.confirmationLog.filter(e => !e.approved).length,
            entries: [...this.confirmationLog]
        };
    }

    async triggerCommand(item) {
        // Immediately schedule like other JSON-based items
        await this.waitAndExecute({
            ...item,
            fromUser: true // make it execute immediately
        });
    }
}