// RenderUtils.js

export class RenderUtils {
    constructor() {
        this.instances = {}; // key: string -> instance
    }

    /**
     * Registers an instance under a given name
     */
    setInstance(name, instance) {
        this.instances[name] = instance;
    }

    /**
     * Gets a registered instance
     */
    getInstance(name) {
        return this.instances[name];
    }

    /**
     * Calls the render method on a registered manager
     */
    render(name, ...args) {
        const instance = this.instances[name];

        if (!instance || typeof instance.render !== 'function') {
            console.warn(`No renderable instance found for "${name}"`);
            return;
        }

        instance.render(...args);
    }

    /**
     * render all known instances
     */
    renderAll() {
        for (const [name, instance] of Object.entries(this.instances)) {
            if (typeof instance.render === 'function') {
                instance.render();
            }
        }
    }
}
