
const { Astar } = require("./astar");

class Thetastar extends Astar {
    constructor(mesh, options) {
        super(mesh, options);
        this.heuristicName = 'euclidean';
    }
    findNeighborParent(currentNode, neighbor) {
        return this.graph.lineOfSightGrid(currentNode.parent, neighbor) ? currentNode.parent : currentNode;
    }
}
module.exports = { Thetastar };