export class Typewriter {
    constructor(textboxId, speed = 25, cursorChar = "█") {
        this.textbox = document.getElementById(textboxId);
        this.speed = speed;
        this.cursorChar = cursorChar;
        this.index = 0;
        this.cursorVisible = true;
        this.message = "";
        this.onComplete = undefined;  // store callback
        this.resolve = null;

        setInterval(() => {
            this.cursorVisible = !this.cursorVisible;
            this.updateText();
        }, 500);
    }

    updateText() {
        const typed = this.message.substring(0, this.index);
        this.textbox.value = typed + (this.cursorVisible ? this.cursorChar : " ");
        this.textbox.scrollTop = this.textbox.scrollHeight;
    }

    typeMessage(newMessage, resolveCallback) {
        this.message = newMessage;
        this.index = 0;
        this.textbox.value = "";
        this.resolve = resolveCallback;
        this.typeCharacter();
    }

    typeCharacter() {
        if (this.index <= this.message.length) {
            this.updateText();
            this.index++;
            setTimeout(() => this.typeCharacter(), this.speed);
        }
        else {
            // ✅ Typing is done, call the resolve callback
            if (typeof this.resolve === "function") {
                this.resolve();
                this.resolve = null;
            }
        }
    }

    clear() {
        this.textbox.value = "";
        this.message = "";
        this.index = 0;
    }
}
