import * as d3 from 'd3';
import {World} from "./world";
import {Chart} from "./chart";
import { envState } from "../settings";
import { renderUtils } from "../newmain";
import '../styles.css';

export class Interface {
    constructor(environment) {
        this.environment = environment;
        this.tabcontainer = undefined;
        this.tabComponents = [];
        this.world = new World(environment);
        this.chart = new Chart(environment);
        this.animationInterval = 0;

        this.init();
    }

    init() {

        window.GlobalEventBus = new EventTarget();

        const self = this;

        // create the tabcontainers
        this.tabcontainer = document.querySelectorAll('.tab-container');
        this.tabcontainer.forEach(container => {
            const tabs = new TabComponent(container, this, (activeId) => {
                // Optional callback example
                //console.log(`Callback says active tab is: ${activeId}`);
            });
            const id = container.getAttribute('id'); // custom attribute
            this.tabComponents.push(tabs); // Save reference
        });

        // Container for controls
        const controls = d3.select("#scene").append("div")
            .style("margin-bottom", "30px")
            .style("margin-top", "1px");

        controls.append("label")
            .text("Random Lines: ")
            .style("margin-right", "25px");

        const inputBox = controls.append("input")
            .attr("type", "number")
            .attr("value", "10")
            .attr("min", "1")
            .attr("max", "100");

        // "Generate Random Lines" button
        controls.append("button")
            .text("Randomize")
            .style("margin-left", "10px")
            .on("click", () => {
                const numLines = parseInt(inputBox.node().value, 10);
                this.environment.generateRandomLines(numLines);
                this.environment.dynamicLines = this.environment.lines;
                this.environment.pathFinding();
                this.environment.particles = []; // clear particles
                document.getElementById("sliderEnd").value = 0;
                this.resetSlider();
                this.environment.update();
                renderUtils.renderAll();
                //socket.emit("generateRandomLines", numLines); // Emit request to generate random lines
            });

        // "Generate Random Lines" button
        controls.append("button")
            .text("Jitter")
            .style("margin-left", "10px")
            .style("margin-right", "10px")
            .on("click", () => {
                const dist = parseInt(inputBox4.node().value, 10);
                this.environment.jitterLines(dist);
                this.environment.dynamicLines = this.environment.lines;
                this.environment.pathFinding();
                this.environment.generateRandomParticles(this.environment.lines,this.environment.numberOfParticles);
                document.getElementById("sliderEnd").value = 0;
                this.resetSlider();
                this.environment.update();
                renderUtils.renderAll();
                //socket.emit("generateRandomLines", numLines); // Emit request to generate random lines
            });

        const inputBox4 = controls.append("input")
            .attr("type", "number")
            .attr("value", "5")
            .attr("min", "1")
            .attr("max", "50");

        controls.append("label")
            .style("display", "block")
            .style("margin-top", "10px")

        controls.append("label")
            .text("Particles per Line: ")
            .style("margin-right", "6px");

        const inputBox3 = controls.append("input")
            .attr("type", "number")
            .attr("value", "10")
            .attr("min", "0")
            .attr("max", "120");

        controls.append("button")
            .text("Randomize")
            .style("margin-left", "10px")
            .on("click", () => {
                this.environment.numberOfParticles = parseInt(inputBox3.node().value, 10); // stores number of particles globally
                this.environment.generateRandomParticles(this.environment.lines, this.environment.numberOfParticles);
                document.getElementById("sliderEnd").value = 0;
                this.resetSlider();
                this.environment.update();
                renderUtils.renderAll();
                //socket.emit("generateVoronoi", {numPoints});
            });

        controls.append("label")
            .style("display", "block")
            .style("margin-top", "10px")

        controls.append("label")
            .text("Voronoi Points: ")
            .style("margin-right", "24px");

        const inputBox2 = controls.append("input")
            .attr("type", "number")
            .attr("value", "10")
            .attr("min", "1")
            .attr("max", "100");

        // "Generate Voronoi" button appears first
        controls.append("button")
            .text("Randomize")
            .style("margin-left", "10px")
            .on("click", () => {
                const numPoints = parseInt(inputBox2.node().value, 10);
                this.environment.generateRandomPoints(numPoints);
                this.environment.generateRandomSpeeds(numPoints);
                this.environment.update();//computeVoronoi();
                renderUtils.renderAll();
                //socket.emit("generateVoronoi", {numPoints});
            });

        // "Cluster Voronoi" button
        controls.append("button")
            .text("Cluster")
            .style("margin-left", "10px")
            .on("click", () => {
                this.world.clusterPoints();
                //socket.emit("generateVoronoi", {numPoints});
            });

        controls.append("button")
            .text("Animate") // initial button text
            .style("margin-left", "10px")
            .on("click", function () {
                if (!envState.isPlaying) {
                    envState.isPlaying = true;
                    self.animationInterval = setInterval(() => self.animatePoints(), 50);
                    renderUtils.renderAll();
                } else {
                    //d3.select(this).text("Animate");
                    envState.isPlaying = false;
                    clearInterval(self.animationInterval);  // Stop animation
                }
                d3.select(this).text(envState.isPlaying ? "Stop" : "Animate");
            });

        // "Save as SVG" button appears below
        controls.append("button")
            .text("Save as SVG")
            .style("display", "block")
            .style("margin-top", "10px")
            .on("click", () => this.world.saveAsSVG());

        // "Save as JSON" button
        controls.append("button")
            .text("Save as JSON")
            .style("display", "block")
            .style("margin-top", "5px")
            .on("click", () => this.environment.saveAsJSON());

        // "Load from JSON" button
        controls.append("button")
            .text("Load from JSON")
            .style("display", "block")
            .style("margin-top", "5px")
            .on("click", () => this.loadFromJSON());

        // init other event handlers related to the html document elements
        document.addEventListener("keydown", (event) => {
            if (event.key === "Control" && !envState.isCtrlPressed) {
                envState.isCtrlPressed = true;
                renderUtils.renderAll();
            }
        });

        document.addEventListener("keyup", (event) => {
            if (event.key === "Control" && envState.isCtrlPressed) {
                envState.isCtrlPressed = false;
                // trigger pathfinder
                this.environment.pathFinding();
                this.environment.generateRandomParticles(this.environment.lines,this.environment.numberOfParticles);
                // and redraw
                this.environment.update();
                renderUtils.renderAll();
            }
        });

        let menu = document.querySelector(".pathfinder-menu");
        menu.addEventListener("change", function() {
            envState.pathFinder = this.value;
            self.environment.pathFinding();
            self.environment.generateRandomParticles(self.environment.lines,self.environment.numberOfParticles);
            // and redraw
            renderUtils.renderAll();
        });

        // Add event listener to slider
        document.getElementById("slider").addEventListener("input", function() {
            if (envState.activeTab === 'environment') {
                document.getElementById("demo").innerText = this.value;
                self.sliderChanged(this.value);
            }
        });

        // Add file input change event to parse JSON
        document.getElementById("fileInput").addEventListener("change", function(event) {
            const file = event.target.files[0];
            if (file) {
                const reader = new FileReader();
                reader.onload = function(e) {
                    try {
                        const jsonData = JSON.parse(e.target.result);  // Parse the JSON data

                        // reset global variables using data structure from parsed JSON
                        self.environment.width = jsonData.dimension.width;
                        self.environment.height = jsonData.dimension.height;

                        // sets the initial zooming viewBox to the new width/height
                        self.initWorld();

                        // init the grid
                        self.environment.initGrid(jsonData.gridSize);
                        envState.pathFinder = jsonData.pathFinder;
                        document.querySelector(".pathfinder-menu").value = envState.pathFinder;

                        self.environment.voronoiPoints = jsonData.voronoiPoints;
                        self.environment.generateRandomSpeeds(self.environment.voronoiPoints.length);
                        self.environment.clippingPolygon = jsonData.clippingPolygon;
                        self.environment.lines = jsonData.lines;
                        self.environment.editPolygons = jsonData.editPolygons;
                        self.environment.editVertices = jsonData.polygonVertices;
                        self.environment.editPolygonIndices = jsonData.polygonIndices;

                        self.environment.lines.dynamicLines = self.environment.lines;
                        self.environment.particles = jsonData.particles;
                        self.environment.numberOfParticles = jsonData.numLineParticles;
                        self.environment.selectedCells = new Set(jsonData.blockedCells);

                        inputBox.attr("value", jsonData.numLines);
                        inputBox2.attr("value", jsonData.numVoronoiPoints);
                        inputBox3.attr("value", self.environment.numberOfParticles);

                        // update voronoi and trigger a redraw
                        self.resetSlider();
                        document.getElementById("sliderEnd").value = 0;

                        envState.editMode = jsonData.mode;

                        self.environment.update();
                        renderUtils.renderAll();

                    } catch (error) {
                        alert("Error parsing JSON file: " + error.message);
                    }
                };
                reader.readAsText(file);
            }
            document.getElementById("fileInput").value = ""; // Reset file input to allow reloading the same file
        });

        // Attach event listeners to checkboxes
        document.getElementById("toggleCrossings").addEventListener("change", () => this.updateVisibility());
        document.getElementById("toggleIntersections").addEventListener("change", () => this.updateVisibility());
        document.getElementById("toggleVoronoiPoints").addEventListener("change", () => this.updateVisibility());
        document.getElementById("toggleLines").addEventListener("change", () => this.updateVisibility());
        document.getElementById("minLength").addEventListener("change", () => this.updateValueSettings());
        document.getElementById("minDistance").addEventListener("change", () => this.updateValueSettings());
        document.getElementById("minVertex").addEventListener("change", () => this.updateValueSettings());
        document.getElementById("toggleDelaunay").addEventListener("change", () => this.updateVisibility());
        document.getElementById("sliderEnd").addEventListener("change", () => this.updateParticleOffset());
        document.getElementById("toggleParticles").addEventListener("change", () => this.updateVisibility());
        document.getElementById("togglePolygons").addEventListener("change", () => this.updateVisibility());
        document.getElementById("toggleDirect").addEventListener("change", () => this.updateVisibility());
        document.getElementById("toggleWeatherCell").addEventListener("change", () => this.updateVisibility());
        document.getElementById("toggleWindField").addEventListener("change", () => this.updateVisibility());

        this.initVisibility();
        this.initValueSettings();

        // init the world and chart
        this.initWorld();
        this.initChart();

        // init canvas event handlers
        this.world.initEventHandlers();
        this.chart.initEventHandlers();

        // draw all components for the first time
        this.render();
    }

    initWorld() {
        this.world.init();
    }

    initChart() {
        this.chart.init();
    }

    sliderChanged(value) {
        this.environment.particlePositions = this.environment.updateParticles(value);
        this.environment.timeValue = parseInt(value);
        this.environment.timeValue += this.environment.timeOffset;
        this.environment.update();
        renderUtils.renderAll();
    }

    resetSlider() {
        document.getElementById("slider").value = 0;
        document.getElementById("slider").max = 120;
        document.getElementById("demo").innerText = "0";
        this.environment.particlePositions = this.environment.updateParticles(0);
        this.environment.timeValue = this.environment.timeOffset;
    }

    updateParticleOffset() {
        const offsetValue = document.getElementById("sliderEnd").value;
        this.environment.offsetParticles(offsetValue);
        this.environment.timeOffset = parseInt(offsetValue);
        this.environment.timeValue += this.environment.timeOffset;
        this.resetSlider();
        this.environment.update();
        renderUtils.renderAll();
    }

    loadFromJSON(filename) {
        if(filename)
            this.loadJSON(filename);
        else
            document.getElementById("fileInput").click(); // opens the file browser
    }

    animatePoints() {
        this.environment.voronoiPoints = this.environment.voronoiPoints.map((point, index) => {
            let [x, y] = point;
            let [vx, vy] = this.environment.voronoiSpeeds[index];

            // Update positions based on "speed" vector
            x += vx;
            y += vy;

            // Bounce off the walls
            if (x <= 0 || x >= this.environment.width) {
                vx *= -1;
                x = Math.max(0, Math.min(x, this.environment.width));  // Keep point within bounds
            }
            if (y <= 0 || y >= this.environment.height) {
                vy *= -1;
                y = Math.max(0, Math.min(y, this.environment.height));  // Keep point within bounds
            }

            // Update speed for bouncing effect
            this.environment.voronoiSpeeds[index] = [vx, vy];

            return [x, y];  // Return updated point position
        });

        this.environment.update();
        this.render();
    }

    async loadJSON(filename) {
        // const filePath = this.path.resolve(__dirname, filename);
        // const rawData = this.fs.readFileSync(filePath, 'utf-8');
        // const jsonData = JSON.parse(rawData);

        const response = await fetch(filename);
        const jsonData = await response.json();

        // reset global variables using data structure from parsed JSON
        this.environment.width = jsonData.dimension.width;
        this.environment.height = jsonData.dimension.height;

        // sets the initial zooming viewBox to the new width/height
        this.initWorld();

        // init the grid
        this.environment.initGrid(jsonData.gridSize);
        envState.pathFinder = jsonData.pathFinder;
        document.querySelector(".pathfinder-menu").value = envState.pathFinder;

        this.environment.voronoiPoints = jsonData.voronoiPoints;
        this.environment.generateRandomSpeeds(this.environment.voronoiPoints.length);
        this.environment.clippingPolygon = jsonData.clippingPolygon;
        this.environment.lines = jsonData.lines;
        this.environment.particles = jsonData.particles;
        this.environment.editPolygons = jsonData.editPolygons;
        this.environment.editVertices = jsonData.polygonVertices;
        this.environment.editPolygonIndices = jsonData.polygonIndices;

        this.environment.dynamicLines = this.environment.lines;
        this.environment.numberOfParticles = jsonData.numLineParticles;
        this.environment.selectedCells = new Set(jsonData.blockedCells);

        // update voronoi and trigger a redraw
        this.resetSlider();

        // reset slider offset
        document.getElementById("sliderEnd").value = 0;

        envState.editMode = jsonData.mode;

        this.environment.update();
        renderUtils.renderAll();
    }

    initVisibility() {
        document.getElementById("toggleCrossings").checked = envState.showCrossings;
        document.getElementById("toggleIntersections").checked = envState.showIntersections;
        document.getElementById("toggleVoronoiPoints").checked = envState.showVoronoiPoints;
        document.getElementById("toggleLines").checked = envState.showLines;
        document.getElementById("toggleDelaunay").checked = envState.showDelaunay;
        document.getElementById("toggleParticles").checked = envState.showParticles;
        document.getElementById("togglePolygons").checked = envState.showPolygons;
        document.getElementById("toggleWeatherCell").checked = envState.showWeather;
        document.getElementById("toggleWindField").checked = envState.showWindField;
        document.getElementById("toggleDirect").checked = envState.showDirectLines;
    }

    initValueSettings() {
        document.getElementById("minLength").value = envState.length_threshold;
        document.getElementById("minDistance").value = envState.distance_threshold;
        document.getElementById("minVertex").value = envState.vertex_line_threshold;
        document.querySelector(".pathfinder-menu").value = envState.pathFinder;
    }

    updateVisibility() {
        envState.showCrossings = document.getElementById("toggleCrossings").checked;
        envState.showIntersections = document.getElementById("toggleIntersections").checked;
        envState.showVoronoiPoints = document.getElementById("toggleVoronoiPoints").checked;
        envState.showLines = document.getElementById("toggleLines").checked;
        envState.showDelaunay = document.getElementById("toggleDelaunay").checked;
        envState.showParticles = document.getElementById("toggleParticles").checked;
        envState.showPolygons = document.getElementById("togglePolygons").checked;
        envState.showWeather = document.getElementById("toggleWeatherCell").checked;
        envState.showWindField = document.getElementById("toggleWindField").checked;
        envState.showDirectLines = document.getElementById("toggleDirect").checked;

        this.environment.update();
        this.render();
    }

    updateValueSettings() {
        envState.length_threshold = document.getElementById("minLength").value;
        envState.distance_threshold = document.getElementById("minDistance").value;
        envState.vertex_line_threshold = document.getElementById("minVertex").value;

        this.environment.update();
        this.render();
    }

    render() {
        renderUtils.renderAll();
    }
}

class TabComponent {
    constructor(container, display, onTabChange = null) {
        this.container = container;
        this.display = display;
        this.tabs = container.querySelectorAll('.tab');
        this.contents = container.querySelectorAll('.tab-content');
        this.onTabChange = onTabChange; // Optional callback
        this.init();
    }

    init() {
        this.tabs.forEach(tab => {
            tab.addEventListener('click', () => this.activateTab(tab));
        });

        // 🔁 Listen for global sync events
        window.GlobalEventBus.addEventListener('sync-tab-state', (e) => {
            this.syncWithExternalState(e.detail); // e.g., envState
        });
    }

    activateTab(selectedTab) {
        this.tabs.forEach(tab => tab.classList.remove('active'));
        this.contents.forEach(content => content.classList.remove('active'));

        selectedTab.classList.add('active');
        selectedTab.classList.remove('tab-warning'); // ✅ Remove warning when activated

        const targetId = selectedTab.getAttribute('data-tab');
        const targetContent = this.container.querySelector(`#${targetId}`);
        if (targetContent) {
            targetContent.classList.add('active');
        }

        // 🔥 Log or use active tab ID
        //console.log(`Active Tab ID: ${targetId}`);

        //envState.activeTab = targetId;
        //console.log(envState.activeTab);

        //console.log(targetId);
        if(targetId === 'environment' || targetId === 'graph' || targetId === 'heatmap') {
            envState.activeTab = targetId;
            const slider = document.getElementById("slider");
            slider.disabled = envState.activeTab !== 'environment';
            envState.assistantTab = null;
            renderUtils.renderAll();
        }
        else if(targetId === 'ai-container'){
            envState.assistantTab = targetId;
        }
        else
            envState.assistantTab = null;

        // 🧠 Optional: Call a callback
        // if (typeof this.onTabChange === 'function') {
        //     this.onTabChange(targetId);
        // }
        //
        // // 📢 Optional: Dispatch custom event
        // this.container.dispatchEvent(new CustomEvent('tabChange', {
        //     detail: { activeTabId: targetId }
        // }));
    }

    // New method to highlight tabs based on a condition
    highlightTabIfNeeded(conditionFn) {
        this.tabs.forEach(tab => {
            if (conditionFn(tab)) {
                tab.classList.add('tab-warning');
            } else {
                tab.classList.remove('tab-warning');
            }
        });
    }

    syncWithExternalState(externalState) {
        this.highlightTabIfNeeded(tab => {
            const tabId = tab.getAttribute('data-tab');
            const isInWarningList = externalState.warningTab.includes(tabId);
            const isActive = tab.classList.contains('active');
            return isInWarningList && !isActive; // ✅ Only highlight if NOT active
        });
    }
}






