// HighlightUtils.js

export class HighlightUtils {
    constructor() {
        this.instances = {};
    }

    setInstance(name, instance) {
        this.instances[name] = instance;
    }

    getInstance(name) {
        return this.instances[name];
    }

    /**
     * Highlights a bar in the registered chart manager.
     * Assumes the chartManager has a method highlightBar(index)
     */
    highlightBar(index) {
        const chart = this.getInstance("chart");

        if (!chart || typeof chart.highlightBar !== "function") {
            console.warn("Chart not set or missing highlightBar()");
            return;
        }

        chart.highlightBar(index);
    }

    removeHighlightBar(index) {
        const chart = this.getInstance("chart");

        if (!chart || typeof chart.removeHighlightBar !== "function") {
            console.warn("Chart not set or missing removeHighlightBar()");
            return;
        }

        chart.removeHighlightBar(index);
    }

    /**
     * Highlights a polygon in the registered polygon manager.
     * Assumes the polygonManager has a method highlightPolygon(index)
     */
    highlightPolygon(index) {
        const poly = this.getInstance("world");

        if (!poly || typeof poly.highlightPolygon !== "function") {
            console.warn("PolygonManager not set or missing highlightPolygon()");
            return;
        }

        poly.highlightPolygon(index);
    }

    removeHighlightPolygon(index) {
        const poly = this.getInstance("world");

        if (!poly || typeof poly.removeHighlightPolygon !== "function") {
            console.warn("PolygonManager not set or missing removeHighlightPolygon()");
            return;
        }

        poly.removeHighlightPolygon(index);
    }

    highlightVoronoi(index) {
        const poly = this.getInstance("world");

        if (!poly || typeof poly.highlightVoronoi !== "function") {
            console.warn("PolygonManager not set or missing highlightVoronoi()");
            return;
        }

        poly.highlightVoronoi(index);
    }

    removeHighlightVoronoi(index) {
        const poly = this.getInstance("world");

        if (!poly || typeof poly.removeHighlightVoronoi !== "function") {
            console.warn("PolygonManager not set or missing removeHighlightVoronoi()");
            return;
        }

        poly.removeHighlightVoronoi(index);
    }
}