export let envState = {};

/**
 * Default environment values
 */
const defaultEnv = {
    activeTab: "environment",
    warningTab: null,
    assistantTab: null,
    editMode: "voronoi",
    pathFinder: "Thetastar",
    assistant: "oz",
    wind: "uniform",
    distance_threshold: 10.0,
    length_threshold: 50.0,
    vertex_line_threshold: 20.0,
    complexity_limit: 60.0,
    isCtrlPressed: false,
    isDragging: false,
    isRightDragging: false,
    isPlaying: false,
    hoveredGridCell: null,
    mouseXPos: -1,
    mouseYPos: -1,
    initTime: 46800,
    transitionDuration: 2000,
    showCrossings: false,
    showIntersections: false,
    showVoronoiPoints: true,
    showLines: true,
    showDelaunay: false,
    showParticles: true,
    showPolygons: true,
    showWeather: false,
    showWindField: false,
    showDirectLines: false
};

/**
 * Loads environment settings from a JSON file and falls back to defaults on failure.
 *
 * @param {string} url - Path to JSON file, e.g., "./env-dev.json"
 * @returns {Promise<void>}
 */
export async function loadEnvSettings(url = './env.json') {
    try {
        const response = await fetch(url);
        if (!response.ok) throw new Error(`HTTP ${response.status} - ${response.statusText}`);
        const data = await response.json();
        Object.assign(envState, defaultEnv, data);  // Fallback for any missing fields
        console.log(`[env] Loaded settings from ${url}`);
    } catch (err) {
        console.warn(`[env] Failed to load '${url}', using fallback defaults.`, err);
        Object.assign(envState, defaultEnv);
    }
}
