___CSS_LOADER_EXPORT___.push([module.id, `.tooltip {
    position: absolute;
    background: rgba(106, 102, 102, 0.8);
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 14px;
    pointer-events: none;
    visibility: hidden;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.cell {
    stroke: #ccc;
    stroke-width: 1px;
}

input[type="file"] {
    display: none; /* Hide the file input */
}

.canvas-container {
    text-align: center;  /* Center align the canvas */
    margin-bottom: 10px; /* Space between canvas and checkboxes */
}

#voronoi-wrapper {
    display: flex;
    flex-direction: column; /* Stack the Voronoi canvas and checkboxes vertically */
    align-items: flex-start; /* Align to the left */
}

.controls {
    text-align: center;  /* Center the checkboxes */
    margin-top: 10px;    /* Space from the canvas */
    font-size: 16px;     /* Make checkboxes easier to read */
}
.controls label {
    display: block;  /* Each checkbox on a new line */
    margin: 5px 0;
}

.variance-box {
    margin-top: 10px;
    padding: 8px;
    /*font-size: 12px;*/
    font-weight: bold;
    width: 80%;
}

.peak-box {
    margin-top: 10px;
    padding: 8px;
    /*font-size: 12px;*/
    font-weight: bold;
    width: 80%;
}

.legend text {
    font-size: 10px;
}

.legend {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
}
.legend-item {
    display: flex;
    align-items: center;
    margin-right: 15px;
}
.legend-rect {
    width: 20px;
    height: 20px;
    margin-right: 5px;
}

#main-container {
    display: flex;
    justify-content: left;
    gap: 20px;
}

#voronoi-container {
    border: 2px solid #ccc;
    padding: 10px;
}

#barchart-container {
    border: 2px solid #ccc;
    padding: 10px;
    cursor: pointer;
}

#settings-container {
    justify-content: stretch;
    width: 100%;
}

.slider-container {
    position: relative;
    width: 700px;
}

/* Style the slider track */
input[type="range"] {
    -webkit-appearance: none; /* Remove default styling */
    appearance: none;
    width: 700px;
    height: 10px; /* Track thickness */
    background: #ddd;
    outline: none;
    opacity: 0.9;
    border-radius: 0; /* Remove round edges */
    margin-top: 10px;
}

/* Square thumb (Chrome, Safari, Edge) */
input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 20px; /* Thumb size */
    height: 20px;
    background: steelblue;
    cursor: pointer;
    border-radius: 0; /* Square shape */
}

/* Square thumb (Firefox) */
input[type="range"]::-moz-range-thumb {
    width: 20px;
    height: 20px;
    background: steelblue;
    cursor: pointer;
    border-radius: 0;
}

input[type="range"]:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.slider-end {
    padding: 0px;
    margin-top: 5px;
    /*text-align: center;*/
}

.gen-container {
    display: flex;
    align-items: flex-start;
    margin-top: 10px;    /* Space from the canvas */
    gap: 10px;
}

