/*    padding: 5px 10px;*/
/*    border-radius: 5px;*/
/*    font-size: 11px;*/
/*    white-space: nowrap;*/
/*}*/

/* Display slider value */
#sliderValue {
    font-size: 18px;
    margin-left: 10px;
}

.context-menu {
    position: absolute;
    background: white;
    border: 1px solid gray;
    display: none;
    z-index: 1000;
    padding: 0;
    list-style: none;
    box-shadow: 2px 2px 5px rgba(0,0,0,0.3);
}
/*.context-menu ul {*/
/*    margin: 0;*/
/*    padding: 0;*/
/*}*/

#context-menu li {
    padding: 8px 12px;
    cursor: pointer;
}
.context-menu li:hover {
    background: lightgray;
}

.min-linelength {
    margin-left: 150px;
    margin-right: 10px;
}

.min-crossdistance {
    margin-left: 89px;
    margin-right: 10px;
}

.min-vertex {
    margin-left: 14px;
    margin-right: 10px;
}

.pathfinder-menu {
    margin-left: 44px;
    margin-right: 2px;
    margin-bottom: 10px;
    margin-top: 20px;
    width: 72px;
    height: 20px;
}

.tabs {
    display: flex;
    cursor: pointer;
    margin-bottom: 1em;
}
.tab {
    padding: 5px 10px;
    font-size: 12pt;
    background-color: #eee;
    border: 1px solid #ccc;
    margin-right: 5px;
    border-radius: 5px 5px 0 0;
}
.tab.active {
    background-color: #fff;
    border-bottom: none;
}
.tab-content {
    /*border: 1px solid #ccc;*/
    padding: 0px;
    display: none;
    /*border-radius: 0 5px 5px 5px;*/
}
.tab-content.active {
    display: block;
}

@keyframes pulse-warning {
    0% { background-color: #e68320; }
    50% { background-color: #f1b979; } /* lighter orange */
    100% { background-color: #e68320; }
}

.tab-warning {
    animation: pulse-warning 1.0s ease-in-out infinite;
    transition: background-color 0.5s ease;
    color: white; /* or your preferred text color */
}

/*.tab-warning {*/
/*    background-color: #e68320;*/
/*    color: white;*/
/*}*/

/* Optional: separation between components */
.tab-container {
    margin-bottom: 0px;
}

.voronoi-point {
    cursor: move;
}

.graphcontrol-point {
    cursor: move;
}

.edit-vertex {
    cursor: move;
}

.clipping-vertex {
    cursor: move;
}

.wind-vector {
    stroke-width: 1.5;
    marker-end: url(#arrow);
}

textarea {
    /*font-family: 'Courier New', Courier, monospace;*/
    font-size: 14px;
    background-color: #efefef;
    color: #6c6767;
    border: 2px solid #afb1af;
    padding: 10px;
    resize: none;
    width: 500px;
    height: 200px;
    overflow-y: scroll;
    white-space: pre-wrap;
    line-height: 1.4em;
}

#oz-confirm-dialog {
    border: 2px solid #aaa;
    padding: 10px;
    width: 500px;
    min-height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #fff;
    transition: opacity 0.3s ease;
    opacity: 1;
}

.oz-dialog-message,
.oz-dialog-buttons {
    display: none;
}

#oz-confirm-dialog.active .oz-dialog-message,
#oz-confirm-dialog.active .oz-dialog-buttons {
    display: block;
    font-family: monospace;
    font-size: 14px;
}

/* Fading out */
#oz-confirm-dialog.fade-out {
    opacity: 0;
}

#oz-confirm-dialog.fade-in {
    opacity: 0;
}
#oz-confirm-dialog.active.fade-in {
    opacity: 1;
    transition: opacity 0.3s ease;
}

